/**
 * Manual Battle Service
 * Handles business logic for manual battle simulation
 * Extracted from ManualBattleSimulationComponent to follow Single Responsibility Principle
 */

import { UnifiedBattleSimulation } from './unified-battle-simulation/UnifiedBattleSimulation';
import { UnifiedBattleConfiguration, UnifiedBattleResult } from './unified-battle-simulation/types';
import { ManualBattleConfiguration, ManualBattleAnalysis, MANUAL_BATTLE_CONSTANTS } from '../types/manualBattle';

export interface BattleAnalysisComparison {
  damageDifference: number;
  survivalDifference: number;
  averageDamage: number;
  averageSurvival: number;
}

/**
 * Service for handling manual battle simulation business logic
 * Separates battle logic from UI concerns in ManualBattleSimulationComponent
 */
export class ManualBattleService {
  constructor(
    private unifiedBattleSimulation: UnifiedBattleSimulation = new UnifiedBattleSimulation()
  ) {}

  /**
   * Convert manual configuration to unified battle format
   * This handles the complex mapping from manual army configuration to the unified battle system
   */
  convertToUnifiedConfig(config: ManualBattleConfiguration, playerGoesFirst: boolean): UnifiedBattleConfiguration {
    return {
      playerArmy: {
        stackingOrder: config.playerArmy.stackingOrder,
        totalStrength: config.playerArmy.totalStrength,
        totalHealth: config.playerArmy.totalHealth
      },
      enemies: config.enemyArmy.stackingOrder.map((group, index) => ({
        type: group.count >= MANUAL_BATTLE_CONSTANTS.INFINITE_UNIT_VALUE ? 'infinite' as const : 'mortal' as const,
        unit: group.unit,
        groupIndex: index,
        count: group.count,
        currentCount: group.count,
      })),
      playerGoesFirst
    };
  }

  /**
   * Calculate battle analysis comparison metrics
   * Compares best case vs worst case battle results
   */
  calculateComparison(bestCase: UnifiedBattleResult, worstCase: UnifiedBattleResult): BattleAnalysisComparison {
    return {
      damageDifference: bestCase.totalDamageDealt - worstCase.totalDamageDealt,
      survivalDifference: bestCase.battleDuration - worstCase.battleDuration,
      averageDamage: (bestCase.totalDamageDealt + worstCase.totalDamageDealt) / 2,
      averageSurvival: (bestCase.battleDuration + worstCase.battleDuration) / 2
    };
  }

  /**
   * Simulate single battle scenario
   * Converts manual config to unified format and runs single simulation
   */
  simulateSingle(config: ManualBattleConfiguration, playerGoesFirst: boolean): UnifiedBattleResult {
    const unifiedConfig = this.convertToUnifiedConfig(config, playerGoesFirst);
    return this.unifiedBattleSimulation.simulateBattle(unifiedConfig);
  }

  /**
   * Simulate both battle scenarios and return full analysis
   * This is the main method that handles both best case (player first) and worst case (enemy first)
   */
  simulateBothScenarios(config: ManualBattleConfiguration): ManualBattleAnalysis {
    const bestCase = this.simulateSingle(config, true);
    const worstCase = this.simulateSingle(config, false);
    
    return {
      bestCase,
      worstCase,
      comparison: this.calculateComparison(bestCase, worstCase),
      manualConfiguration: config
    };
  }

  /**
   * Validate manual battle configuration
   * Currently returns valid for all configurations
   * Validation logic can be moved here from component if needed for consistency
   */
  validateConfiguration(config: ManualBattleConfiguration): { isValid: boolean; errors: string[] } {
    // For now, we keep the existing validation in the component for immediate UI feedback
    // This method is here for potential future enhancement and API consistency
    return { isValid: true, errors: [] };
  }
}