/**
 * Unified Battle Simulation - Core Types and Interfaces
 * 
 * This file defines the core types and interfaces for the new unified battle simulation system.
 * It replaces the complex hierarchy of battle simulation services with clean, shared interfaces.
 */

import { Unit, StackingGroup, AttackModifier } from '../../types/units';
import { CombatAction } from '../../types/battleSimulation';

// Re-export types that are used by other files
export type { Unit } from '../../types/units';

/**
 * Types of enemies supported by the unified battle simulation
 */
export type EnemyType = 'generic' | 'infinite' | 'mortal';

/**
 * Generic enemy configuration - simple enemies that always kill
 */
export interface GenericEnemy {
  type: 'generic';
  name: string;
  groupIndex: number; // For combat log identification
}

/**
 * Infinite enemy configuration - specific enemies with unlimited quantity
 */
export interface InfiniteEnemy {
  type: 'infinite';
  unit: Unit;
  groupIndex: number;
}

/**
 * Mortal enemy configuration - specific enemies with finite counts
 */
export interface MortalEnemy {
  type: 'mortal';
  unit: Unit;
  count: number;
  currentCount: number; // Tracks remaining units
  groupIndex: number;
}

/**
 * Union type for all enemy configurations
 */
export type Enemy = GenericEnemy | InfiniteEnemy | MortalEnemy;

/**
 * Player group wrapper for battle state
 */
export interface BattlePlayerGroup extends StackingGroup {
  // Additional battle-specific properties can be added here if needed
}

/**
 * Battle configuration for unified simulation
 */
export interface UnifiedBattleConfiguration {
  playerArmy: {
    stackingOrder: StackingGroup[];
    totalStrength: number;
    totalHealth: number;
  };
  enemies: Enemy[];
  playerGoesFirst: boolean;
  battleName?: string;
  useEnemyModifiers?: boolean;
  /** Override maximum battle turns for testing (default: 50000) */
  maxBattleTurns?: number;
  /** Override maximum actions per turn for testing (default: 1000) */
  maxActionsPerTurn?: number;
}

/**
 * Action tracking for turn management
 * Tracks which unit groups have taken their action this turn
 */
export interface ActionTracking {
  players: Set<string>; // Player unit group names that have taken action this turn
  enemies: Set<number>; // Enemy indices that have taken action this turn
}

/**
 * Unified battle state
 */
export interface UnifiedBattleState {
  currentTurn: number;
  currentAction: number; // Action index within current turn
  playerGroups: BattlePlayerGroup[];
  enemies: Enemy[];
  unitsActedThisTurn: ActionTracking; // Which unit groups have taken action this turn
  combatLog: CombatAction[];
  battleEnded: boolean;
  totalDamageDealt: number;
}

/**
 * Attack result from processing a single attack
 */
export interface AttackResult {
  damageDealt: number;
  bonusDamageDealt: number | undefined;
  modifierName: string | undefined;
  targetEliminated: boolean;
  attackerEliminated: boolean; // For counter-attacks or special abilities
  combatAction: CombatAction;
}

/**
 * Battle outcome
 */
export interface BattleOutcome {
  result: 'player_victory' | 'player_eliminated' | 'stalemate';
  survivingPlayerGroups: BattlePlayerGroup[];
  survivingEnemies: Enemy[];
  totalTurns: number;
  totalActions: number;
}

/**
 * Battle result containing full battle information
 */
export interface UnifiedBattleResult {
  outcome: BattleOutcome;
  combatLog: CombatAction[];
  totalDamageDealt: number;
  battleDuration: number; // in turns
}

/**
 * Interface for enemy type handlers
 */
export interface EnemyTypeHandler<T extends Enemy = Enemy> {
  canAttack(enemy: T): boolean;
  calculateDamage(enemy: T, target: BattlePlayerGroup): number;
  processAttack(enemy: T, target: BattlePlayerGroup): AttackResult;
  isEliminated(enemy: T): boolean;
  getDisplayName(enemy: T): string;
  getStrength(enemy: T): number;
}

/**
 * Interface for determining the next attacker based on current battle state
 */
export interface NextAttackerSelector {
  /**
   * Determines the next player unit that should attack based on maximum damage potential
   * Units that can deal the most damage to enemies attack first
   * Dynamic recalculation each action based on remaining alive units
   */
  selectNextPlayer(state: UnifiedBattleState): BattlePlayerGroup | null;
  
  /**
   * Determines the next enemy unit that should attack based on maximum damage potential  
   * Each attacking unit targets the enemy it does the most damage to
   * Dynamic recalculation each action based on remaining alive enemies
   */
  selectNextEnemy(state: UnifiedBattleState): Enemy | null;
}

/**
 * Interface for turn management following strict battle mechanics
 */
export interface TurnManager {
  /**
   * Determines if the next action should be performed by a player unit group
   * Handles alternation and uneven unit group scenarios
   */
  shouldPlayerActNext(state: UnifiedBattleState, playerGoesFirst: boolean): boolean;
  
  /**
   * Gets player unit groups that haven't acted this turn and are still alive
   */
  getAvailablePlayerGroups(state: UnifiedBattleState): BattlePlayerGroup[];
  
  /**
   * Gets enemy unit groups that haven't acted this turn and can still attack
   */
  getAvailableEnemies(state: UnifiedBattleState): Enemy[];
  
  /**
   * Returns new state with unit marked as having taken its action for this turn
   * Pure function - does not mutate input state
   */
  markUnitAsActed(state: UnifiedBattleState, unit: BattlePlayerGroup | Enemy): UnifiedBattleState;
  
  /**
   * Determines if the current turn is complete (all unit groups have acted or been eliminated)
   */
  isTurnComplete(state: UnifiedBattleState): boolean;
  
  /**
   * Returns new state advanced to the next turn with reset action tracking
   * Pure function - does not mutate input state
   */
  advanceToNextTurn(state: UnifiedBattleState): UnifiedBattleState;
  
  /**
   * Returns new state with turn tracking reset (for battle restart)
   * Pure function - does not mutate input state
   */
  resetTurn(state: UnifiedBattleState): UnifiedBattleState;

  /**
   * Check if the battle should end (no units can act in future turns)
   */
  shouldBattleEnd(state: UnifiedBattleState): boolean;
}

/**
 * Detailed damage breakdown for enhanced combat logging
 */
export interface DamageBreakdown {
  baseDamage: number;
  finalDamage: number;
  bonusDamage: number;
  appliedModifiers: Array<AttackModifier>;
}

/**
 * Complete attack plan with all necessary information calculated once
 */
export interface AttackPlan {
  attacker: BattlePlayerGroup | Enemy;
  target: BattlePlayerGroup | Enemy;
  damageDealt: number;
  damageBreakdown?: DamageBreakdown; // Optional detailed damage information
  targetEliminated: boolean;
  combatAction: CombatAction;
}

/**
 * Damage calculation result with modifiers
 */
export interface DamageCalculationResult {
  baseDamage: number;
  modifiedDamage: number;
  appliedModifiers: AttackModifier[];
  criticalHit: boolean;
  damageType: 'normal' | 'instant_kill';
}

/**
 * Target selection strategies
 */
export type TargetSelectionStrategy = 
  | 'maximum_damage'
  | 'eliminate_first' 
  | 'weakest_first'
  | 'strongest_first';

/**
 * Target selection result
 */
export interface TargetSelectionResult {
  target: BattlePlayerGroup | Enemy | null;
  reason: string;
  estimatedDamage?: number;
}

/**
 * Enhanced enemy type handler with additional methods
 */
export interface EnemyTypeHandler<T extends Enemy = Enemy> {
  canAttack(enemy: T): boolean;
  calculateDamage(enemy: T, target: BattlePlayerGroup): number;
  processAttack(enemy: T, target: BattlePlayerGroup): AttackResult;
  processDamageTaken?(enemy: T, damage: number): { enemyEliminated: boolean; unitsLost: number };
  isEliminated(enemy: T): boolean;
  getDisplayName(enemy: T): string;
  getStrength(enemy: T): number;
  getEnemyType(): EnemyType;
  
  // Attack order optimization methods
  getMaxPotentialDamage(enemy: T, availableTargets?: BattlePlayerGroup[]): number;
  calculateTotalPotentialDamage(enemy: T, availableTargets: BattlePlayerGroup[]): number;
}

/**
 * Enhanced next attacker selector with target selection
 */
export interface NextAttackerSelector {
  /**
   * Determines the next player unit that should attack and returns complete attack plan
   */
  selectNextPlayerAttack(state: UnifiedBattleState): AttackPlan | null;
  
  /**
   * Determines the next enemy unit that should attack and returns complete attack plan
   */
  selectNextEnemyAttack(state: UnifiedBattleState): AttackPlan | null;

  // Legacy methods for backward compatibility
  selectNextPlayer(state: UnifiedBattleState): BattlePlayerGroup | null;
  selectNextEnemy(state: UnifiedBattleState): Enemy | null;
  selectBestTargetForPlayer(attacker: BattlePlayerGroup, targets: Enemy[]): Enemy | null;
  selectBestTargetForEnemy(attacker: Enemy, targets: BattlePlayerGroup[]): BattlePlayerGroup | null;
}

/**
 * Constants for unified battle simulation
 */
export const UNIFIED_BATTLE_CONSTANTS = {
  MAX_BATTLE_TURNS: 50000, // Very high limit to avoid interfering with legitimate long battles
  MAX_ACTIONS_PER_TURN: 1000, // High limit for battles with many unit groups
  GENERIC_ENEMY_DAMAGE: 999999999 // Always kills
} as const;