/**
 * Turn Manager
 * 
 * Pure function-based turn management for unified battle simulation.
 * Manages turn progression and strict alternation between player and enemy actions
 * without mutating the input state - returns new state objects instead.
 * 
 * Battle Mechanics:
 * - Turn: A complete round where all available unit groups have the opportunity to take one action
 * - Action: A single attack performed by one unit group against another unit group
 * - Strict alternation: Player Action  Enemy Action  Player Action  Enemy Action
 * - Uneven groups: Side with remaining units continues consecutively when the other side is exhausted
 * 
 * Architecture:
 * - Pure functions: All methods return new state objects, never mutate input
 * - Immutable state: Maintains battle state integrity and enables history tracking
 * - Functional composition: State transformations can be composed and tested in isolation
 */

import { 
  UnifiedBattleState, 
  BattlePlayerGroup, 
  Enemy, 
  TurnManager as ITurnManager,
  ActionTracking
} from '../types';

export class TurnManager implements ITurnManager {
  
  /**
   * Determines if the next action should be performed by a player unit group
   * Handles alternation and uneven unit group scenarios
   */
  public shouldPlayerActNext(state: UnifiedBattleState, playerGoesFirst: boolean): boolean {
    const availablePlayers = this.getAvailablePlayerGroups(state);
    const availableEnemies = this.getAvailableEnemies(state);

    // If one side has no units available, the other side acts
    if (availablePlayers.length === 0) {
      return false; // Only enemies can act
    }
    if (availableEnemies.length === 0) {
      return true; // Only players can act
    }

    // Both sides have available units - use strict alternation
    const isEvenAction = state.currentAction % 2 === 0;
    
    if (playerGoesFirst) {
      return isEvenAction; // Player acts on even actions (0, 2, 4...)
    } else {
      return !isEvenAction; // Player acts on odd actions (1, 3, 5...)
    }
  }

  /**
   * Gets player unit groups that haven't acted this turn and are still alive
   */
  public getAvailablePlayerGroups(state: UnifiedBattleState): BattlePlayerGroup[] {
    return state.playerGroups.filter(group => 
      group.totalHealth > 0 && 
      !state.unitsActedThisTurn.players.has(group.unit.name)
    );
  }

  /**
   * Gets enemy unit groups that haven't acted this turn and can still attack
   */
  public getAvailableEnemies(state: UnifiedBattleState): Enemy[] {
    return state.enemies.filter((enemy, index) => 
      this.canEnemyAttack(enemy) && 
      !state.unitsActedThisTurn.enemies.has(index)
    );
  }

  /**
   * Returns new state with unit marked as having taken its action for this turn
   * Pure function - does not mutate input state
   */
  public markUnitAsActed(state: UnifiedBattleState, unit: BattlePlayerGroup | Enemy): UnifiedBattleState {
    const newUnitsActedThisTurn: ActionTracking = {
      players: new Set(state.unitsActedThisTurn.players),
      enemies: new Set(state.unitsActedThisTurn.enemies)
    };

    if (this.isPlayerGroup(unit)) {
      // Player unit
      newUnitsActedThisTurn.players.add(unit.unit.name);
    } else {
      // Enemy unit - find its index by groupIndex (more reliable than reference comparison)
      const enemyIndex = state.enemies.findIndex(e => e.groupIndex === unit.groupIndex);
      if (enemyIndex >= 0) {
        newUnitsActedThisTurn.enemies.add(enemyIndex);
      } else {
        console.warn('Could not find enemy index for marking as acted:', unit);
      }
    }
    
    return {
      ...state,
      currentAction: state.currentAction + 1,
      unitsActedThisTurn: newUnitsActedThisTurn
    };
  }

  /**
   * Determines if the current turn is complete (all unit groups have acted or been eliminated)
   * A turn is complete when all units that could potentially act this turn have either:
   * 1. Already acted (marked in unitsActedThisTurn), OR
   * 2. Been eliminated/cannot act anymore
   */
  public isTurnComplete(state: UnifiedBattleState): boolean {
    const availablePlayers = this.getAvailablePlayerGroups(state);
    const availableEnemies = this.getAvailableEnemies(state);
    
    // Turn is complete when no more units from either side can act THIS TURN
    return availablePlayers.length === 0 && availableEnemies.length === 0;
  }

  /**
   * Check if the battle should end (no units can act in future turns)
   */
  public shouldBattleEnd(state: UnifiedBattleState): boolean {
    const alivePlayerGroups = state.playerGroups.filter(group => group.totalHealth > 0);
    const attackableEnemies = state.enemies.filter(enemy => this.canEnemyAttack(enemy));
    
    // Battle ends if either side has no units that can act
    return alivePlayerGroups.length === 0 || attackableEnemies.length === 0;
  }

  /**
   * Returns new state advanced to the next turn with reset action tracking
   * Pure function - does not mutate input state
   */
  public advanceToNextTurn(state: UnifiedBattleState): UnifiedBattleState {
    return {
      ...state,
      currentTurn: state.currentTurn + 1,
      currentAction: 0,
      unitsActedThisTurn: {
        players: new Set<string>(),
        enemies: new Set<number>()
      }
    };
  }

  /**
   * Check if an enemy can currently attack based on its type and state
   */
  private canEnemyAttack(enemy: Enemy): boolean {
    switch (enemy.type) {
      case 'generic':
        return true; // Generic enemies always attack
      case 'infinite':
        return true; // Infinite enemies always attack
      case 'mortal':
        return enemy.currentCount > 0; // Mortal enemies attack if they have units left
      default:
        return false;
    }
  }

  /**
   * Type guard to check if a unit is a player group
   */
  private isPlayerGroup(unit: BattlePlayerGroup | Enemy): unit is BattlePlayerGroup {
    return 'unit' in unit && 'count' in unit && 'totalHealth' in unit;
  }

  /**
   * Get battle turn statistics for debugging/analysis
   */
  public getTurnStats(state: UnifiedBattleState): {
    currentTurn: number;
    currentAction: number;
    actionsThisTurn: number;
    playersActedThisTurn: number;
    enemiesActedThisTurn: number;
    availablePlayerGroups: number;
    availableEnemies: number;
    turnComplete: boolean;
  } {
    return {
      currentTurn: state.currentTurn,
      currentAction: state.currentAction,
      actionsThisTurn: state.unitsActedThisTurn.players.size + state.unitsActedThisTurn.enemies.size,
      playersActedThisTurn: state.unitsActedThisTurn.players.size,
      enemiesActedThisTurn: state.unitsActedThisTurn.enemies.size,
      availablePlayerGroups: this.getAvailablePlayerGroups(state).length,
      availableEnemies: this.getAvailableEnemies(state).length,
      turnComplete: this.isTurnComplete(state)
    };
  }

  /**
   * Validate turn state consistency
   */
  public validateTurnState(state: UnifiedBattleState): {
    isValid: boolean;
    errors: string[];
    warnings: string[];
  } {
    const errors: string[] = [];
    const warnings: string[] = [];

    // Check for negative values
    if (state.currentTurn < 1) {
      errors.push(`Invalid turn number: ${state.currentTurn} (should be >= 1)`);
    }
    
    if (state.currentAction < 0) {
      errors.push(`Invalid action number: ${state.currentAction} (should be >= 0)`);
    }

    // Check action tracking consistency
    const totalActionsTracked = state.unitsActedThisTurn.players.size + state.unitsActedThisTurn.enemies.size;
    if (totalActionsTracked > state.currentAction) {
      warnings.push(`More units marked as acted (${totalActionsTracked}) than current action count (${state.currentAction})`);
    }

    // Check for units marked as acted that no longer exist or can't act
    for (const playerName of state.unitsActedThisTurn.players) {
      const player = state.playerGroups.find(p => p.unit.name === playerName);
      if (!player) {
        warnings.push(`Player ${playerName} marked as acted but not found in battle`);
      } else if (player.totalHealth <= 0) {
        warnings.push(`Dead player ${playerName} marked as acted this turn`);
      }
    }

    for (const enemyIndex of state.unitsActedThisTurn.enemies) {
      const enemy = state.enemies[enemyIndex];
      if (!enemy) {
        warnings.push(`Enemy at index ${enemyIndex} marked as acted but not found`);
      } else if (!this.canEnemyAttack(enemy)) {
        warnings.push(`Enemy ${enemy.type} at index ${enemyIndex} marked as acted but cannot attack`);
      }
    }

    return {
      isValid: errors.length === 0,
      errors,
      warnings
    };
  }

  /**
   * Get detailed alternation analysis for debugging
   */
  public getAlternationAnalysis(state: UnifiedBattleState, playerGoesFirst: boolean): {
    expectedPattern: string;
    actualPattern: string;
    isCorrectAlternation: boolean;
    nextShouldBe: 'player' | 'enemy';
  } {
    const availablePlayers = this.getAvailablePlayerGroups(state);
    const availableEnemies = this.getAvailableEnemies(state);

    // Build expected pattern based on available units
    let expectedPattern = '';
    let currentAction = 0;
    const totalPlayers = availablePlayers.length + state.unitsActedThisTurn.players.size;
    const totalEnemies = availableEnemies.length + state.unitsActedThisTurn.enemies.size;
    const totalActions = Math.max(totalPlayers + totalEnemies, state.currentAction);

    for (let i = 0; i < totalActions; i++) {
      const shouldBePlayer = this.shouldPlayerActNext({ ...state, currentAction: i }, playerGoesFirst);
      expectedPattern += shouldBePlayer ? 'P' : 'E';
    }

    // Build actual pattern from action tracking (simplified)
    const actualPattern = 'P'.repeat(state.unitsActedThisTurn.players.size) + 'E'.repeat(state.unitsActedThisTurn.enemies.size);

    const nextShouldBe = this.shouldPlayerActNext(state, playerGoesFirst) ? 'player' : 'enemy';

    return {
      expectedPattern,
      actualPattern,
      isCorrectAlternation: true, // TODO: Implement proper pattern matching
      nextShouldBe
    };
  }

  /**
   * Returns new state with turn tracking reset (for battle restart)
   * Pure function - does not mutate input state
   */
  public resetTurn(state: UnifiedBattleState): UnifiedBattleState {
    return {
      ...state,
      currentTurn: 1,
      currentAction: 0,
      unitsActedThisTurn: {
        players: new Set<string>(),
        enemies: new Set<number>()
      }
    };
  }

  /**
   * Get turn progression summary
   */
  public getTurnSummary(state: UnifiedBattleState): string {
    const stats = this.getTurnStats(state);
    return `Turn ${stats.currentTurn}, Action ${stats.currentAction}: ${stats.playersActedThisTurn}P + ${stats.enemiesActedThisTurn}E acted, ${stats.availablePlayerGroups}P + ${stats.availableEnemies}E available`;
  }
}