/**
 * Mortal Enemy Handler
 * 
 * Handles specific enemy units with finite counts that can be eliminated.
 * These enemies have specific stats, attack modifiers, and can be killed
 * by player units, making them the most complex enemy type.
 */

import { 
  EnemyTypeHandler, 
  MortalEnemy, 
  BattlePlayerGroup, 
  AttackResult 
} from '../types';
import { CombatAction } from '../../../types/battleSimulation';

export class MortalEnemyHandler implements EnemyTypeHandler<MortalEnemy> {
  
  /**
   * Mortal enemies can attack if they have remaining units
   */
  public canAttack(enemy: MortalEnemy): boolean {
    return enemy.currentCount > 0;
  }

  /**
   * Calculate damage a mortal enemy deals to a player unit group
   * Damage scales with the number of remaining enemy units
   */
  public calculateDamage(enemy: MortalEnemy, target: BattlePlayerGroup): number {
    if (enemy.currentCount <= 0) {
      return 0;
    }

    // Base damage from all remaining enemy units
    const baseDamage = enemy.currentCount * enemy.unit.strength;
    
    // Check for attack modifiers against the target
    const modifierPercentage = this.getBestAttackModifier(enemy, target);
    
    if (modifierPercentage !== 0) {
      // Apply percentage modifier (e.g., 50 = 50% increase)
      // Use original strength for attack modifier calculations
      const baseStrengthForModifiers = enemy.unit.originalStrength || enemy.unit.strength;
      const modifierDamage = Math.floor(enemy.currentCount * baseStrengthForModifiers * (modifierPercentage / 100));
      return baseDamage + modifierDamage;
    }
    
    return baseDamage;
  }

  /**
   * Process a mortal enemy attack against a player unit group
   */
  public processAttack(enemy: MortalEnemy, target: BattlePlayerGroup): AttackResult {
    const actualDamage = this.calculateActualDamage(enemy, target);
    const actualDamageDealt = actualDamage.healthLost;
    
    const targetEliminated = actualDamageDealt >= target.totalHealth;
    
    // Create combat action (will be populated by the caller with turn info)
    const combatAction: CombatAction = {
      turn: 0, // Will be set by caller
      attacker: this.getDisplayName(enemy),
      target: target.unit.name,
      action: `attack ${target.unit.name} for ${actualDamageDealt} damage${targetEliminated ? ' (ELIMINATED)' : ''}`,
      damageDealt: actualDamageDealt,
      eliminated: targetEliminated
    };

    return {
      damageDealt: actualDamageDealt,
      targetEliminated,
      attackerEliminated: false, // Mortal enemies can be eliminated, but not by their own attacks
      combatAction
    };
  }

  /**
   * Mortal enemies are eliminated when their count reaches zero
   */
  public isEliminated(enemy: MortalEnemy): boolean {
    return enemy.currentCount <= 0;
  }

  /**
   * Get display name for combat logs
   */
  public getDisplayName(enemy: MortalEnemy): string {
    return `${enemy.unit.name} (${enemy.currentCount}/${enemy.count})`;
  }

  /**
   * Get strength for damage calculations and comparisons
   * For mortal enemies, total strength depends on remaining count
   */
  public getStrength(enemy: MortalEnemy): number {
    return enemy.currentCount * enemy.unit.strength;
  }

  /**
   * Calculate actual damage to be applied to player unit group
   */
  public calculateActualDamage(enemy: MortalEnemy, target: BattlePlayerGroup): {
    healthLost: number;
    unitsKilled: number;
  } {
    const totalDamage = this.calculateDamage(enemy, target);
    
    // Calculate how many units are killed
    const unitsKilled = Math.min(
      Math.floor(totalDamage / target.unit.health),
      target.count
    );
    
    // Calculate actual health lost
    const healthLost = Math.min(totalDamage, target.totalHealth);
    
    return {
      healthLost,
      unitsKilled
    };
  }

  /**
   * Process damage dealt TO a mortal enemy (when player attacks)
   * Returns number of enemy units killed
   */
  public processDamageTaken(enemy: MortalEnemy, damageReceived: number): {
    unitsKilled: number;
    remainingCount: number;
    enemyEliminated: boolean;
  } {
    if (damageReceived <= 0 || enemy.currentCount <= 0) {
      return {
        unitsKilled: 0,
        remainingCount: enemy.currentCount,
        enemyEliminated: this.isEliminated(enemy)
      };
    }

    // Calculate how many enemy units are killed by this damage
    const unitsKilled = Math.min(
      Math.floor(damageReceived / enemy.unit.health),
      enemy.currentCount
    );

    // Update the enemy's current count
    enemy.currentCount = Math.max(0, enemy.currentCount - unitsKilled);

    return {
      unitsKilled,
      remainingCount: enemy.currentCount,
      enemyEliminated: this.isEliminated(enemy)
    };
  }

  /**
   * Get the best attack modifier against a target
   */
  private getBestAttackModifier(enemy: MortalEnemy, target: BattlePlayerGroup): number {
    if (!enemy.unit.attack_modifiers || enemy.unit.attack_modifiers.length === 0) {
      return 0;
    }

    // If target has unit data with unit_types, check for modifiers
    if (target.unit?.unit_types) {
      let bestModifier = 0;
      
      for (const modifier of enemy.unit.attack_modifiers) {
        // Check if modifier applies to any of the target's unit types
        if (target.unit.unit_types.includes(modifier.target_type) && 
            modifier.modifier_type === 'Strength') {
          bestModifier = Math.max(bestModifier, modifier.value);
        }
      }
      
      return bestModifier;
    }
    
    // If no unit data available, return 0 (no modifiers can be applied)
    return 0;
  }

  /**
   * Check if this enemy has targeting preferences based on attack modifiers
   */
  public hasTargetPreference(enemy: MortalEnemy): boolean {
    return enemy.unit.attack_modifiers && enemy.unit.attack_modifiers.length > 0;
  }

  /**
   * Get targeting priority score for a specific target
   * Higher scores indicate better targets for this enemy
   */
  public getTargetPriority(enemy: MortalEnemy, target: BattlePlayerGroup): number {
    const basePriority = 1.0;
    const modifier = this.getBestAttackModifier(enemy, target);
    
    if (modifier > 0) {
      // Targets with damage bonuses get higher priority
      return basePriority + (modifier / 100);
    }
    
    return basePriority;
  }

  /**
   * Check if this enemy can be counter-attacked
   * Mortal enemies can be attacked and eliminated
   */
  public canBeCounterAttacked(enemy: MortalEnemy): boolean {
    return enemy.currentCount > 0;
  }

  /**
   * Get total health of all remaining enemy units
   */
  public getTotalHealth(enemy: MortalEnemy): number {
    return enemy.currentCount * enemy.unit.health;
  }

  /**
   * Get individual unit health
   */
  public getUnitHealth(enemy: MortalEnemy): number {
    return enemy.unit.health;
  }

  /**
   * Get enemy unit types (for targeting calculations)
   */
  public getUnitTypes(enemy: MortalEnemy): string[] {
    return enemy.unit.unit_types || [];
  }

  /**
   * Get enemy attack modifiers (for damage calculations)
   */
  public getAttackModifiers(enemy: MortalEnemy): any[] {
    return enemy.unit.attack_modifiers || [];
  }

  /**
   * Get current count of remaining units
   */
  public getCurrentCount(enemy: MortalEnemy): number {
    return enemy.currentCount;
  }

  /**
   * Get original count of units
   */
  public getOriginalCount(enemy: MortalEnemy): number {
    return enemy.count;
  }

  /**
   * Get enemy type identifier
   */
  public getEnemyType(): 'mortal' {
    return 'mortal';
  }

  /**
   * Calculate maximum potential damage this enemy can deal to the best target
   * Used for attack order prioritization - this should be the damage to the single best target,
   * not total damage to all targets (that's calculated by the attack selector)
   */
  public getMaxPotentialDamage(enemy: MortalEnemy, availableTargets?: BattlePlayerGroup[]): number {
    if (enemy.currentCount <= 0) {
      return 0;
    }

    const baseDamage = enemy.currentCount * enemy.unit.strength;
    
    if (!availableTargets || availableTargets.length === 0) {
      // Return base damage if no targets specified
      return baseDamage;
    }
    
    // Find the target this enemy would deal the most damage to
    let maxDamage = baseDamage;
    
    for (const target of availableTargets) {
      const damageToThisTarget = this.calculateDamage(enemy, target);
      maxDamage = Math.max(maxDamage, damageToThisTarget);
    }
    
    return maxDamage;
  }

  /**
   * Calculate total potential damage this enemy could deal to all available targets
   * Used by attack selector to determine which unit should attack next
   */
  public calculateTotalPotentialDamage(enemy: MortalEnemy, availableTargets: BattlePlayerGroup[]): number {
    if (enemy.currentCount <= 0) {
      return 0;
    }

    let totalDamage = 0;
    
    for (const target of availableTargets) {
      totalDamage += this.calculateDamage(enemy, target);
    }
    
    return totalDamage;
  }

  /**
   * Reset enemy to original count (for battle restarts)
   */
  public resetToOriginalCount(enemy: MortalEnemy): void {
    enemy.currentCount = enemy.count;
  }
}