/**
 * Unified Battle Simulation
 * 
 * Main battle simulation class that handles all enemy types with shared logic.
 * Replaces the complex hierarchy of battle simulation services with a single, unified system.
 */

import { 
  UnifiedBattleConfiguration,
  UnifiedBattleResult,
  UnifiedBattleState,
  BattleOutcome,
  Enemy,
  EnemyTypeHandler,
  NextAttackerSelector,
  UNIFIED_BATTLE_CONSTANTS
} from './types';
import { BattleState } from './BattleState';
import { DynamicAttackerSelector } from './NextAttackerSelector';
import { EnemyFactory } from './enemies/EnemyFactory';
import { TurnManager } from './turns/TurnManager';

export class UnifiedBattleSimulation {
  private battleState!: BattleState;
  private attackerSelector: NextAttackerSelector;
  private enemyFactory: EnemyFactory;
  private turnManager: TurnManager;

  constructor() {
    this.attackerSelector = new DynamicAttackerSelector();
    this.enemyFactory = EnemyFactory.getInstance();
    this.turnManager = new TurnManager();
    this.validateEnemyHandlers();
  }

  /**
   * Simulate a battle with the given configuration
   */
  public simulateBattle(config: UnifiedBattleConfiguration): UnifiedBattleResult {
    // Initialize battle state
    this.battleState = new BattleState(config);
    
    // Validate configuration
    this.validateConfiguration(config);
    
    // Run the battle simulation loop
    this.runBattleLoop(config.playerGoesFirst, config);
    
    // Calculate final results
    return this.buildBattleResult(config);
  }

  /**
   * Main battle loop following strict turn alternation mechanics using dedicated TurnManager
   */
  private runBattleLoop(playerGoesFirst: boolean, config: UnifiedBattleConfiguration): void {
    let actionCount = 0;
    const maxTurns = config.maxBattleTurns ?? UNIFIED_BATTLE_CONSTANTS.MAX_BATTLE_TURNS;
    const maxActionsPerTurn = config.maxActionsPerTurn ?? UNIFIED_BATTLE_CONSTANTS.MAX_ACTIONS_PER_TURN;
    const maxActionsPerBattle = maxTurns * maxActionsPerTurn;
    
    while (!this.battleState.isBattleEnded() && actionCount < maxActionsPerBattle) {
      const currentState = this.battleState.getBattleState();
      
      // Determine who should act next based on alternation rules using TurnManager
      const shouldPlayerAct = this.turnManager.shouldPlayerActNext(currentState, playerGoesFirst);
      
      
      if (shouldPlayerAct) {
        this.processPlayerAction(currentState);
      } else {
        this.processEnemyAction(currentState);
      }
      
      actionCount++;
      
      // Check if battle should end IMMEDIATELY after action (most important check)
      const stateAfterAction = this.battleState.getBattleState();
      if (this.turnManager.shouldBattleEnd(stateAfterAction)) {
        const endState = this.battleState.checkBattleEndPure(stateAfterAction, maxTurns);
        this.battleState.applyStateUpdate(endState);
        break;
      }
      
      // Check other battle end conditions  
      const finalState = this.battleState.checkBattleEndPure(stateAfterAction, maxTurns);
      this.battleState.applyStateUpdate(finalState);
      
      // Check if turn is complete AFTER processing actions
      const finalStateAfterBattleCheck = this.battleState.getBattleState();
      if (!this.battleState.isBattleEnded() && this.turnManager.isTurnComplete(finalStateAfterBattleCheck)) {
        const newState = this.turnManager.advanceToNextTurn(finalStateAfterBattleCheck);
        this.battleState.applyStateUpdate(newState);
      }
    }
    
    // Force end if we hit the action limit (stalemate protection)
    if (actionCount >= maxActionsPerBattle) {
      this.battleState.endBattle();
    }
  }

  /**
   * Process a player unit group action (efficient unified approach)
   */
  private processPlayerAction(currentState: UnifiedBattleState): void {
    // Get complete attack plan with all information calculated once
    const attackPlan = this.attackerSelector.selectNextPlayerAttack(currentState);
    if (!attackPlan) {
      return; // No available players to act
    }
    
    // Apply complete attack plan (damage, combat log, mark as acted) in one operation
    const newState = this.battleState.applyCompleteAttack(currentState, attackPlan);
    this.battleState.applyStateUpdate(newState);
  }

  /**
   * Process an enemy unit action (efficient unified approach)
   */
  private processEnemyAction(currentState: UnifiedBattleState): void {
    // Get complete attack plan with all information calculated once
    const attackPlan = this.attackerSelector.selectNextEnemyAttack(currentState);
    if (!attackPlan) {
      return; // No available enemies to act
    }
    
    // Apply complete attack plan (damage, combat log, mark as acted) in one operation
    const newState = this.battleState.applyCompleteAttack(currentState, attackPlan);
    this.battleState.applyStateUpdate(newState);
  }



  /**
   * Validate that all required enemy handlers are available
   */
  private validateEnemyHandlers(): void {
    const validation = this.enemyFactory.validateHandlers();
    if (!validation.isValid) {
      throw new Error(`Missing enemy type handlers: ${validation.missingTypes.join(', ')}`);
    }
  }

  /**
   * Validate the battle configuration
   */
  private validateConfiguration(config: UnifiedBattleConfiguration): void {
    if (!config.playerArmy || !config.playerArmy.stackingOrder || config.playerArmy.stackingOrder.length === 0) {
      throw new Error('Player army must have at least one unit group');
    }
    
    if (!config.enemies || config.enemies.length === 0) {
      throw new Error('Battle must have at least one enemy');
    }
    
    // Validate enemy configurations
    for (const enemy of config.enemies) {
      if (enemy.type === 'mortal' && (!enemy.count || enemy.count <= 0)) {
        throw new Error(`Mortal enemy ${enemy.unit?.name || 'Unknown'} must have a positive count`);
      }
    }
  }

  /**
   * Build the final battle result
   */
  private buildBattleResult(config: UnifiedBattleConfiguration): UnifiedBattleResult {
    const state = this.battleState.getBattleState();
    const maxTurns = config.maxBattleTurns ?? UNIFIED_BATTLE_CONSTANTS.MAX_BATTLE_TURNS;
    const outcome = this.battleState.getBattleOutcome(maxTurns);
    
    const battleOutcome: BattleOutcome = {
      result: outcome,
      survivingPlayerGroups: this.battleState.getAlivePlayerGroups(),
      survivingEnemies: this.battleState.getAttackableEnemies(),
      totalTurns: this.battleState.getCurrentTurn(),
      totalActions: state.currentAction + (this.battleState.getCurrentTurn() - 1) * UNIFIED_BATTLE_CONSTANTS.MAX_ACTIONS_PER_TURN
    };
    
    return {
      outcome: battleOutcome,
      combatLog: this.battleState.getCombatLog(),
      totalDamageDealt: this.battleState.getTotalDamageDealt(),
      battleDuration: Math.ceil(battleOutcome.totalTurns) // Duration in turns, not milliseconds
    };
  }

  /**
   * Get current battle state for debugging/monitoring
   */
  public getBattleState(): Readonly<UnifiedBattleState> {
    return this.battleState?.getBattleState() || {} as UnifiedBattleState;
  }

  /**
   * Check if the battle simulation is currently running
   */
  public isBattleActive(): boolean {
    return this.battleState ? !this.battleState.isBattleEnded() : false;
  }
}