/**
 * Battle Logger
 * 
 * Unified combat log generation and management system.
 * Provides consistent logging format and detailed battle analysis
 * for debugging, replay, and battle visualization purposes.
 */

import { 
  BattlePlayerGroup, 
  Enemy, 
  UnifiedBattleState,
  AttackResult
} from '../types';
import { CombatAction } from '../../../types/battleSimulation';

export interface BattleLogEntry extends CombatAction {
  // Extended properties for unified battle system
  attackerSide: 'player' | 'enemy';
  timestamp: number;
  turnSummary?: string;
  actionDetails?: string;
  stateSnapshot?: {
    alivePlayerGroups: number;
    attackableEnemies: number;
    totalPlayerHealth: number;
  };
}

export interface BattleLogSummary {
  totalTurns: number;
  totalActions: number;
  totalDamageDealt: number;
  playerActionsCount: number;
  enemyActionsCount: number;
  eliminationsCount: number;
  battleDuration: number; // in turns
  outcome: string;
}

export class BattleLogger {
  private logs: BattleLogEntry[] = [];
  private startTime: number = 0;
  private detailedLogging: boolean = true;

  constructor(detailedLogging: boolean = true) {
    this.detailedLogging = detailedLogging;
    this.startTime = Date.now();
  }

  /**
   * Log a player action against an enemy
   */
  public logPlayerAction(
    attacker: BattlePlayerGroup,
    target: Enemy,
    attackResult: AttackResult,
    turn: number,
    actionIndex: number,
    battleState?: UnifiedBattleState
  ): BattleLogEntry {
    const targetName = this.getEnemyDisplayName(target);
    
    const logEntry: BattleLogEntry = {
      turn,
      attacker: `${attacker.count}x ${attacker.unit.name}`,
      target: targetName,
      action: `attack ${targetName} for ${attackResult.damageDealt} damage${!!attackResult.bonusDamageDealt ? ` including ${attackResult.bonusDamageDealt} bonus damage vs ${attackResult.modifierName}` : '' }${attackResult.targetEliminated ? ' (ELIMINATED)' : ''}`,
      damageDealt: attackResult.damageDealt,
      eliminated: attackResult.targetEliminated,
      attackerSide: 'player',
      timestamp: Date.now() - this.startTime
    };

    if (this.detailedLogging) {
      logEntry.turnSummary = this.generateTurnSummary(turn, actionIndex);
      logEntry.actionDetails = this.generatePlayerActionDetails(attacker, target, attackResult);
      
      if (battleState) {
        logEntry.stateSnapshot = this.createStateSnapshot(battleState);
      }
    }

    this.logs.push(logEntry);
    return logEntry;
  }

  /**
   * Log an enemy action against a player unit group
   */
  public logEnemyAction(
    attacker: Enemy,
    target: BattlePlayerGroup,
    attackResult: AttackResult,
    turn: number,
    actionIndex: number,
    battleState?: UnifiedBattleState
  ): BattleLogEntry {
    const logEntry: BattleLogEntry = {
      turn,
      attacker: this.getEnemyDisplayName(attacker),
      target: target.unit.name,
      action: `attack ${target.unit.name} for ${attackResult.damageDealt} damage${attackResult.targetEliminated ? ' (ELIMINATED)' : ''}`,
      damageDealt: attackResult.damageDealt,
      eliminated: attackResult.targetEliminated,
      attackerSide: 'enemy',
      timestamp: Date.now() - this.startTime
    };

    if (this.detailedLogging) {
      logEntry.turnSummary = this.generateTurnSummary(turn, actionIndex);
      logEntry.actionDetails = this.generateEnemyActionDetails(attacker, target, attackResult);
      
      if (battleState) {
        logEntry.stateSnapshot = this.createStateSnapshot(battleState);
      }
    }

    this.logs.push(logEntry);
    return logEntry;
  }

  /**
   * Log a turn transition
   */
  public logTurnTransition(
    previousTurn: number,
    newTurn: number,
    battleState: UnifiedBattleState
  ): void {
    if (!this.detailedLogging) return;

    const logEntry: BattleLogEntry = {
      turn: newTurn,
      actionIndex: 0,
      attackerName: 'SYSTEM',
      attackerCount: 0,
      targetName: 'TURN_TRANSITION',
      targetType: 'system',
      damageDealt: 0,
      targetEliminated: false,
      attackerSide: 'system',
      timestamp: Date.now() - this.startTime,
      turnSummary: `Turn ${previousTurn} completed, advancing to Turn ${newTurn}`,
      actionDetails: `Battle continues with ${this.countAlivePlayerGroups(battleState)} player groups vs ${this.countAttackableEnemies(battleState)} enemies`,
      stateSnapshot: this.createStateSnapshot(battleState)
    };

    this.logs.push(logEntry);
  }

  /**
   * Log battle start
   */
  public logBattleStart(
    playerGroups: BattlePlayerGroup[],
    enemies: Enemy[],
    playerGoesFirst: boolean
  ): void {
    const logEntry: BattleLogEntry = {
      turn: 1,
      actionIndex: 0,
      attackerName: 'SYSTEM',
      attackerCount: 0,
      targetName: 'BATTLE_START',
      targetType: 'system',
      damageDealt: 0,
      targetEliminated: false,
      attackerSide: 'system',
      timestamp: 0,
      turnSummary: 'Battle begins',
      actionDetails: `${playerGroups.length} player groups vs ${enemies.length} enemies, ${playerGoesFirst ? 'Player' : 'Enemy'} goes first`,
      stateSnapshot: {
        alivePlayerGroups: playerGroups.length,
        attackableEnemies: enemies.length,
        totalPlayerHealth: playerGroups.reduce((sum, group) => sum + group.totalHealth, 0)
      }
    };

    this.logs.push(logEntry);
  }

  /**
   * Log battle end
   */
  public logBattleEnd(
    outcome: string,
    battleState: UnifiedBattleState,
    reason: string = 'Battle completed'
  ): void {
    const logEntry: BattleLogEntry = {
      turn: battleState.currentTurn,
      actionIndex: battleState.currentAction,
      attackerName: 'SYSTEM',
      attackerCount: 0,
      targetName: 'BATTLE_END',
      targetType: 'system',
      damageDealt: battleState.totalDamageDealt,
      targetEliminated: false,
      attackerSide: 'system',
      timestamp: Date.now() - this.startTime,
      turnSummary: `Battle ended: ${outcome}`,
      actionDetails: `${reason}. Total damage dealt: ${battleState.totalDamageDealt}`,
      stateSnapshot: this.createStateSnapshot(battleState)
    };

    this.logs.push(logEntry);
  }

  /**
   * Get all battle logs
   */
  public getBattleLogs(): BattleLogEntry[] {
    return [...this.logs];
  }

  /**
   * Get logs for a specific turn
   */
  public getLogsForTurn(turn: number): BattleLogEntry[] {
    return this.logs.filter(log => log.turn === turn);
  }

  /**
   * Get logs for a specific side (player/enemy/system)
   */
  public getLogsBySide(side: 'player' | 'enemy' | 'system'): BattleLogEntry[] {
    return this.logs.filter(log => log.attackerSide === side);
  }

  /**
   * Generate battle summary
   */
  public generateBattleSummary(): BattleLogSummary {
    const playerActions = this.logs.filter(log => log.attackerSide === 'player');
    const enemyActions = this.logs.filter(log => log.attackerSide === 'enemy');
    const eliminations = this.logs.filter(log => log.targetEliminated);
    
    const lastLog = this.logs[this.logs.length - 1];
    const battleDuration = lastLog ? lastLog.turn : 0;
    const totalDamage = this.logs
      .filter(log => log.attackerSide !== 'system')
      .reduce((sum, log) => sum + log.damageDealt, 0);

    return {
      totalTurns: battleDuration,
      totalActions: playerActions.length + enemyActions.length,
      totalDamageDealt: totalDamage,
      playerActionsCount: playerActions.length,
      enemyActionsCount: enemyActions.length,
      eliminationsCount: eliminations.length,
      battleDuration,
      outcome: this.determineBattleOutcome()
    };
  }

  /**
   * Export logs in different formats
   */
  public exportLogs(format: 'json' | 'csv' | 'text' = 'json'): string {
    switch (format) {
      case 'json':
        return JSON.stringify(this.logs, null, 2);
      case 'csv':
        return this.exportToCSV();
      case 'text':
        return this.exportToText();
      default:
        return JSON.stringify(this.logs, null, 2);
    }
  }

  /**
   * Clear all logs (for new battle)
   */
  public clearLogs(): void {
    this.logs = [];
    this.startTime = Date.now();
  }

  /**
   * Set detailed logging mode
   */
  public setDetailedLogging(enabled: boolean): void {
    this.detailedLogging = enabled;
  }

  /**
   * Search logs by criteria
   */
  public searchLogs(criteria: {
    attacker?: string;
    target?: string;
    minDamage?: number;
    turnRange?: [number, number];
    eliminationOnly?: boolean;
  }): BattleLogEntry[] {
    return this.logs.filter(log => {
      if (criteria.attacker && !log.attackerName.toLowerCase().includes(criteria.attacker.toLowerCase())) {
        return false;
      }
      if (criteria.target && !log.targetName.toLowerCase().includes(criteria.target.toLowerCase())) {
        return false;
      }
      if (criteria.minDamage && log.damageDealt < criteria.minDamage) {
        return false;
      }
      if (criteria.turnRange && (log.turn < criteria.turnRange[0] || log.turn > criteria.turnRange[1])) {
        return false;
      }
      if (criteria.eliminationOnly && !log.targetEliminated) {
        return false;
      }
      return true;
    });
  }

  // Private helper methods

  private getEnemyDisplayName(enemy: Enemy): string {
    switch (enemy.type) {
      case 'generic':
        return enemy.name || 'Generic Enemy';
      case 'infinite':
        return enemy.unit?.name || 'Infinite Enemy';
      case 'mortal':
        return enemy.unit?.name || 'Mortal Enemy';
      default:
        return 'Unknown Enemy';
    }
  }

  private getEnemyCount(enemy: Enemy): number {
    switch (enemy.type) {
      case 'generic':
        return 1;
      case 'infinite':
        return 999999; // Represents infinite
      case 'mortal':
        return enemy.currentCount;
      default:
        return 0;
    }
  }

  private generateTurnSummary(turn: number, actionIndex: number): string {
    return `Turn ${turn}, Action ${actionIndex}`;
  }

  private generatePlayerActionDetails(
    attacker: BattlePlayerGroup,
    target: Enemy,
    result: AttackResult
  ): string {
    const targetName = this.getEnemyDisplayName(target);
    let details = `${attacker.count}x ${attacker.unitName} attacks ${targetName}`;
    details += ` for ${result.damageDealt} damage`;
    
    if (result.targetEliminated) {
      details += ` (ELIMINATED)`;
    }
    
    return details;
  }

  private generateEnemyActionDetails(
    attacker: Enemy,
    target: BattlePlayerGroup,
    result: AttackResult
  ): string {
    const attackerName = this.getEnemyDisplayName(attacker);
    let details = `${attackerName}`;
    
    if (attacker.type === 'mortal') {
      details += ` (${attacker.currentCount} units)`;
    }
    
    details += ` attacks ${target.count}x ${target.unitName}`;
    details += ` for ${result.damageDealt} damage`;
    
    if (result.targetEliminated) {
      details += ` (ELIMINATED)`;
    }
    
    return details;
  }

  private createStateSnapshot(battleState: UnifiedBattleState): {
    alivePlayerGroups: number;
    attackableEnemies: number;
    totalPlayerHealth: number;
  } {
    return {
      alivePlayerGroups: this.countAlivePlayerGroups(battleState),
      attackableEnemies: this.countAttackableEnemies(battleState),
      totalPlayerHealth: battleState.playerGroups.reduce((sum, group) => sum + group.totalHealth, 0)
    };
  }

  private countAlivePlayerGroups(battleState: UnifiedBattleState): number {
    return battleState.playerGroups.filter(group => group.totalHealth > 0).length;
  }

  private countAttackableEnemies(battleState: UnifiedBattleState): number {
    return battleState.enemies.filter(enemy => {
      switch (enemy.type) {
        case 'generic':
        case 'infinite':
          return true;
        case 'mortal':
          return enemy.currentCount > 0;
        default:
          return false;
      }
    }).length;
  }

  private determineBattleOutcome(): string {
    const lastSystemLog = [...this.logs].reverse().find(log => log.attackerSide === 'system' && log.targetName === 'BATTLE_END');
    if (lastSystemLog && lastSystemLog.turnSummary) {
      return lastSystemLog.turnSummary.replace('Battle ended: ', '');
    }
    return 'Unknown';
  }

  private exportToCSV(): string {
    const headers = ['Turn', 'Action', 'Attacker Side', 'Attacker', 'Target', 'Damage', 'Eliminated', 'Timestamp'];
    const rows = this.logs.map(log => [
      log.turn,
      log.actionIndex,
      log.attackerSide,
      `${log.attackerCount}x ${log.attackerName}`,
      log.targetName,
      log.damageDealt,
      log.targetEliminated ? 'Yes' : 'No',
      log.timestamp
    ]);

    return [headers, ...rows].map(row => row.join(',')).join('\n');
  }

  private exportToText(): string {
    const summary = this.generateBattleSummary();
    let text = `Battle Summary:\n`;
    text += `Total Turns: ${summary.totalTurns}\n`;
    text += `Total Actions: ${summary.totalActions}\n`;
    text += `Total Damage: ${summary.totalDamageDealt}\n`;
    text += `Player Actions: ${summary.playerActionsCount}\n`;
    text += `Enemy Actions: ${summary.enemyActionsCount}\n`;
    text += `Eliminations: ${summary.eliminationsCount}\n`;
    text += `Outcome: ${summary.outcome}\n\n`;

    text += `Detailed Combat Log:\n`;
    text += `${'='.repeat(50)}\n`;

    for (const log of this.logs) {
      if (log.attackerSide === 'system') {
        text += `[SYSTEM] ${log.turnSummary}: ${log.actionDetails}\n`;
      } else {
        text += `[Turn ${log.turn}.${log.actionIndex}] ${log.actionDetails}\n`;
      }
    }

    return text;
  }
}