/**
 * Generic Enemy Handler
 * 
 * Handles simple generic enemies that always kill player units.
 * These enemies represent basic threats in battle scenarios where
 * the focus is on player unit optimization rather than complex enemy mechanics.
 */

import { 
  EnemyTypeHandler, 
  GenericEnemy, 
  BattlePlayerGroup, 
  AttackResult,
  UNIFIED_BATTLE_CONSTANTS 
} from '../types';
import { CombatAction } from '../../../types/battleSimulation';

export class GenericEnemyHandler implements EnemyTypeHandler<GenericEnemy> {
  
  /**
   * Generic enemies can always attack (they never get eliminated)
   */
  public canAttack(enemy: GenericEnemy): boolean {
    return true;
  }

  /**
   * Generic enemies always deal maximum damage (always kill)
   */
  public calculateDamage(enemy: GenericEnemy, target: BattlePlayerGroup): number {
    return UNIFIED_BATTLE_CONSTANTS.GENERIC_ENEMY_DAMAGE;
  }

  /**
   * Process a generic enemy attack against a player unit group
   * Generic enemies always eliminate the target completely
   */
  public processAttack(enemy: GenericEnemy, target: BattlePlayerGroup): AttackResult {
    const damageDealt = this.calculateDamage(enemy, target);
    
    // Generic enemies always eliminate the entire unit group
    const targetEliminated = true;
    
    // Create combat action (will be populated by the caller with turn info)
    const combatAction: CombatAction = {
      turn: 0, // Will be set by caller
      attacker: this.getDisplayName(enemy),
      target: target.unit.name,
      action: `attack and eliminate ${target.unit.name}`,
      damageDealt: damageDealt,
      eliminated: targetEliminated
    };

    return {
      damageDealt,
      targetEliminated,
      attackerEliminated: false, // Generic enemies never get eliminated
      combatAction
    };
  }

  /**
   * Generic enemies are never eliminated
   */
  public isEliminated(enemy: GenericEnemy): boolean {
    return false;
  }

  /**
   * Get display name for combat logs
   */
  public getDisplayName(enemy: GenericEnemy): string {
    return enemy.name;
  }

  /**
   * Get strength for damage calculations and comparisons
   */
  public getStrength(enemy: GenericEnemy): number {
    return UNIFIED_BATTLE_CONSTANTS.GENERIC_ENEMY_DAMAGE;
  }

  /**
   * Get enemy type identifier
   */
  public getEnemyType(): 'generic' {
    return 'generic';
  }

  /**
   * Calculate maximum potential damage this enemy can deal to the best target
   * For generic enemies, this is always infinite damage (always kill)
   */
  public getMaxPotentialDamage(enemy: GenericEnemy, availableTargets?: BattlePlayerGroup[]): number {
    return UNIFIED_BATTLE_CONSTANTS.GENERIC_ENEMY_DAMAGE;
  }

  /**
   * Calculate total potential damage this enemy could deal to all available targets
   * For generic enemies, this is infinite damage times number of targets
   */
  public calculateTotalPotentialDamage(enemy: GenericEnemy, availableTargets: BattlePlayerGroup[]): number {
    // Generic enemies always kill, so total damage is infinite regardless of number of targets
    // But we can represent this as infinite damage per target
    return UNIFIED_BATTLE_CONSTANTS.GENERIC_ENEMY_DAMAGE * availableTargets.length;
  }

  /**
   * Calculate actual damage to be applied to player unit group
   * For generic enemies, this eliminates the entire group
   */
  public calculateActualDamage(enemy: GenericEnemy, target: BattlePlayerGroup): {
    healthLost: number;
    unitsKilled: number;
  } {
    return {
      healthLost: target.totalHealth, // Kill all health
      unitsKilled: target.count // Kill all units
    };
  }

  /**
   * Check if this enemy has any special targeting preferences
   * Generic enemies don't have preferences - they attack whatever they're told to
   */
  public hasTargetPreference(enemy: GenericEnemy): boolean {
    return false;
  }

  /**
   * Get targeting priority score for a specific target
   * Generic enemies treat all targets equally
   */
  public getTargetPriority(enemy: GenericEnemy, target: BattlePlayerGroup): number {
    return 1.0; // All targets are equal priority
  }

  /**
   * Check if this enemy can be counter-attacked
   * Generic enemies cannot be counter-attacked (they're abstract threats)
   */
  public canBeCounterAttacked(enemy: GenericEnemy): boolean {
    return false;
  }

}