/**
 * Shared utility for displaying unified battle results
 * Used by both BattleSimulationComponent and ManualBattleSimulationComponent
 */

import { UnifiedBattleResult } from '../../services/unified-battle-simulation/types';

export interface BattleResultsDisplayOptions {
  showComparison?: boolean;
  title?: string;
  subtitle?: string;
  includeTooltips?: boolean;
  onLogTabChange?: () => void;
}

export class BattleResultsDisplay {
  /**
   * Generate HTML for displaying unified battle results
   */
  static generateResultsHTML(
    bestCase: UnifiedBattleResult,
    worstCase: UnifiedBattleResult,
    options: BattleResultsDisplayOptions = {}
  ): string {
    const { showComparison = true, title = "Battle Analysis Results", subtitle } = options;

    // Calculate comparison metrics
    const damageDifference = bestCase.totalDamageDealt - worstCase.totalDamageDealt;
    const survivalDifference = bestCase.battleDuration - worstCase.battleDuration;
    const averageDamage = (bestCase.totalDamageDealt + worstCase.totalDamageDealt) / 2;

    const headerSection = `
      <div class="results-header">
        <h3> ${title}
          <span class="help-icon" data-tooltip="battle-results"></span>
        </h3>
        ${subtitle ? `<p class="results-summary">${subtitle}</p>` : `
          <p class="results-summary">
            Simulation complete! Here's how your army performs in battle scenarios.
          </p>
        `}
      </div>
    `;

    const scenarioSection = `
      <div class="scenario-comparison">
        <div class="scenario-card best-case">
          <h4> Best Case Scenario
            <span class="help-icon" data-tooltip="best-case-scenario"></span>
          </h4>
          <p class="scenario-description">Your army attacks first</p>
          <div class="scenario-stats">
            <div class="stat">
              <span class="stat-label">Damage Dealt:</span>
              <span class="stat-value">${bestCase.totalDamageDealt.toLocaleString()}</span>
            </div>
            <div class="stat">
              <span class="stat-label">Battle Duration:</span>
              <span class="stat-value">${bestCase.battleDuration} turns</span>
            </div>
            <div class="stat">
              <span class="stat-label">Total Actions:</span>
              <span class="stat-value">${bestCase.outcome.totalActions}</span>
            </div>
          </div>
        </div>

        <div class="scenario-card worst-case">
          <h4> Worst Case Scenario
            <span class="help-icon" data-tooltip="worst-case-scenario"></span>
          </h4>
          <p class="scenario-description">Enemy forces attack first</p>
          <div class="scenario-stats">
            <div class="stat">
              <span class="stat-label">Damage Dealt:</span>
              <span class="stat-value">${worstCase.totalDamageDealt.toLocaleString()}</span>
            </div>
            <div class="stat">
              <span class="stat-label">Battle Duration:</span>
              <span class="stat-value">${worstCase.battleDuration} turns</span>
            </div>
            <div class="stat">
              <span class="stat-label">Total Actions:</span>
              <span class="stat-value">${worstCase.outcome.totalActions}</span>
            </div>
          </div>
        </div>
      </div>
    `;

    const comparisonSection = showComparison ? `
      <div class="comparison-analysis">
        <h4> Scenario Comparison</h4>
        <div class="comparison-stats">
          <div class="comparison-stat">
            <span class="stat-label">Damage Difference:</span>
            <span class="stat-value ${damageDifference >= 0 ? 'positive' : 'negative'}">
              ${damageDifference >= 0 ? '+' : ''}${damageDifference.toLocaleString()}
            </span>
          </div>
          <div class="comparison-stat">
            <span class="stat-label">Survival Difference:</span>
            <span class="stat-value ${survivalDifference >= 0 ? 'positive' : 'negative'}">
              ${survivalDifference >= 0 ? '+' : ''}${survivalDifference} turns
            </span>
          </div>
          <div class="comparison-stat">
            <span class="stat-label">Average Damage:</span>
            <span class="stat-value">${Math.round(averageDamage).toLocaleString()}</span>
          </div>
        </div>
        <div class="comparison-insights">
          <p class="insight">
            ${damageDifference > 0 
              ? ` <strong>Initiative Advantage:</strong> Attacking first provides ${damageDifference.toLocaleString()} extra damage.`
              : damageDifference < 0
              ? ` <strong>Initiative Disadvantage:</strong> Enemy first strike reduces your damage by ${Math.abs(damageDifference).toLocaleString()}.`
              : ` <strong>Balanced Combat:</strong> Turn order has minimal impact on damage output.`
            }
          </p>
        </div>
      </div>
    ` : '';

    const detailedLogsSection = `
      <div class="detailed-logs">
        <div class="log-tabs">
          <button class="log-tab active" data-scenario="best">Best Case Combat Log</button>
          <button class="log-tab" data-scenario="worst">Worst Case Combat Log</button>
        </div>
        <div class="log-content">
          <div id="best-case-log" class="combat-log active">
            ${this.formatCombatLog(bestCase.combatLog)}
          </div>
          <div id="worst-case-log" class="combat-log hidden">
            ${this.formatCombatLog(worstCase.combatLog)}
          </div>
        </div>
      </div>
    `;

    return headerSection + scenarioSection + comparisonSection + detailedLogsSection;
  }

  /**
   * Display battle results in a container element
   */
  static displayResults(
    container: HTMLElement,
    bestCase: UnifiedBattleResult,
    worstCase: UnifiedBattleResult,
    options: BattleResultsDisplayOptions = {}
  ): void {
    container.innerHTML = this.generateResultsHTML(bestCase, worstCase, options);
    
    // Attach log tab listeners
    this.attachLogTabListeners(container, options.onLogTabChange);
  }

  /**
   * Format combat log for display
   */
  private static formatCombatLog(combatLog: any[]): string {
    if (combatLog.length === 0) {
      return '<p class="no-log">No combat actions recorded.</p>';
    }

    const logEntries = combatLog.map(action => {
      // Use attackerSide to determine styling - player attacks are blue, enemy attacks are red
      const isPlayerTurn = action.attackerSide === 'player';
      const turnClass = isPlayerTurn ? 'log-entry player-turn' : 'log-entry enemy-turn';

      return `
        <div class="${turnClass}">
          <span class="turn-number">Turn ${action.turn}:</span>
          <span class="action-text">${action.attacker} ${action.action}</span>
        </div>
      `;
    }).join('');

    return `<div class="log-entries">${logEntries}</div>`;
  }

  /**
   * Attach log tab event listeners
   */
  private static attachLogTabListeners(container: HTMLElement, onTabChange?: () => void): void {
    const logTabs = container.querySelectorAll('.log-tab');
    logTabs.forEach(tab => {
      tab.addEventListener('click', (e) => {
        const target = e.target as HTMLElement;
        const scenario = target.dataset.scenario;
        
        // Update active tab
        logTabs.forEach(t => t.classList.remove('active'));
        target.classList.add('active');
        
        // Show corresponding log
        const logs = container.querySelectorAll('.combat-log');
        logs.forEach(log => {
          log.classList.remove('active');
          log.classList.add('hidden');
        });
        
        const activeLog = container.querySelector(`#${scenario}-case-log`);
        if (activeLog) {
          activeLog.classList.add('active');
          activeLog.classList.remove('hidden');
        }

        // Call callback if provided
        if (onTabChange) {
          onTabChange();
        }
      });
    });
  }
}