/**
 * Next Attacker Selector
 * 
 * Dynamic attack order selection based on maximum single-target damage potential.
 * For each available unit group, calculates damage to each possible target (including modifiers).
 * The unit group that can deal the highest damage to any single target gets to attack next.
 * Recalculates each action to ensure optimal targeting and attack sequence.
 */

import { 
  UnifiedBattleState, 
  BattlePlayerGroup, 
  Enemy, 
  NextAttackerSelector,
  AttackPlan,
  DamageBreakdown,
  UNIFIED_BATTLE_CONSTANTS
} from './types';
import { AttackModifier } from '../../types/units';
import { Unit } from '../../types/units';

export class DynamicAttackerSelector implements NextAttackerSelector {
  
  /**
   * Efficient method: Determines the next player attack and returns complete attack plan
   * Calculates everything once: attacker, target, damage, elimination, combat action
   */
  public selectNextPlayerAttack(state: UnifiedBattleState): AttackPlan | null {
    
    const availablePlayers = state.playerGroups.filter(group => 
      group.totalHealth > 0 && 
      !state.unitsActedThisTurn.players.has(group.unit.name)
    );

    if (availablePlayers.length === 0) {
      return null;
    }

    const attackableEnemies = this.getAttackableEnemies(state);
    if (attackableEnemies.length === 0) {
      return null;
    }

    // Find the player-target combination with highest damage
    let bestAttackPlan: AttackPlan | null = null;
    let maxDamage = 0;

    for (const player of availablePlayers) {
      for (const enemy of attackableEnemies) {
        const damageResult = this.calculatePlayerDamageToEnemy(player, enemy);
        
        if (damageResult.finalDamage > maxDamage) {
          maxDamage = damageResult.finalDamage;
          
          // Calculate if target would be eliminated
          let targetEliminated = false;
          if (enemy.type === 'mortal') {
            const enemyTotalHealth = enemy.unit.health * enemy.currentCount;
            targetEliminated = damageResult.finalDamage >= enemyTotalHealth;
          }
          // Generic and infinite enemies cannot be eliminated by player attacks

          // Create attack plan (combat log will be generated centrally in BattleState)
          bestAttackPlan = {
            attacker: player,
            target: enemy,
            damageDealt: damageResult.finalDamage,
            damageBreakdown: damageResult,
            targetEliminated,
            combatAction: {} as any // Legacy field, actual logging handled in BattleState
          };
        }
      }
    }

    return bestAttackPlan;
  }

  /**
   * Efficient method: Determines the next enemy attack and returns complete attack plan  
   * Calculates everything once: attacker, target, damage, elimination, combat action
   */
  public selectNextEnemyAttack(state: UnifiedBattleState): AttackPlan | null {
    const availableEnemies = state.enemies.filter((enemy, index) => 
      this.canEnemyAttack(enemy) && 
      !state.unitsActedThisTurn.enemies.has(index)
    );

    if (availableEnemies.length === 0) {
      return null;
    }

    const alivePlayers = state.playerGroups.filter(group => group.totalHealth > 0);
    if (alivePlayers.length === 0) {
      return null;
    }

    // Find the enemy-target combination with highest damage
    let bestAttackPlan: AttackPlan | null = null;
    let maxDamage = 0;

    for (const enemy of availableEnemies) {
      for (const player of alivePlayers) {
        const damageDealt = this.calculateEnemyDamageToPlayer(enemy, player);
        
        if (damageDealt.finalDamage > maxDamage) {
          maxDamage = damageDealt.finalDamage;
          
          // Calculate if target would be eliminated
          const targetEliminated = damageDealt.finalDamage >= player.totalHealth;

          // Create attack plan (combat log will be generated centrally in BattleState)
          bestAttackPlan = {
            attacker: enemy,
            target: player,
            damageDealt: damageDealt.finalDamage,
            damageBreakdown: damageDealt,
            targetEliminated,
            combatAction: {} as any // Legacy field, actual logging handled in BattleState
          };
        }
      }
    }

    return bestAttackPlan;
  }

  /**
   * Helper: Get enemy display name for consistent naming
   */
  public getEnemyDisplayName(enemy: Enemy): string {
    switch (enemy.type) {
      case 'generic':
        return enemy.name;
      case 'infinite':
        return enemy.unit.name;
      case 'mortal':
        return `${enemy.unit.name} (${enemy.currentCount}/${enemy.count})`;
      default:
        return 'Unknown Enemy';
    }
  }

  /**
   * Legacy method: Determines the next player unit that should attack based on maximum single-target damage potential
   * For each available player unit group, calculates damage to each available enemy target (including modifiers)
   * The unit group that can deal the highest damage to any single target gets to attack next
   * Dynamic recalculation each action based on remaining alive units
   */
  public selectNextPlayer(state: UnifiedBattleState): BattlePlayerGroup | null {
    const availablePlayers = state.playerGroups.filter(group => 
      group.totalHealth > 0 && 
      !state.unitsActedThisTurn.players.has(group.unit.name)
    );

    if (availablePlayers.length === 0) {
      return null;
    }

    const attackableEnemies = this.getAttackableEnemies(state);
    if (attackableEnemies.length === 0) {
      return null;
    }

    // Find the player unit group that can deal the highest damage to any single target
    let bestPlayer: BattlePlayerGroup | null = null;
    let maxSingleTargetDamage = 0;

    for (const player of availablePlayers) {
      const playerMaxSingleTargetDamage = this.calculatePlayerMaxDamage(player, attackableEnemies);
      
      if (playerMaxSingleTargetDamage > maxSingleTargetDamage) {
        maxSingleTargetDamage = playerMaxSingleTargetDamage;
        bestPlayer = player;
      }
    }

    return bestPlayer;
  }

  /**
   * Determines the next enemy unit that should attack based on maximum single-target damage potential  
   * For each available enemy unit group, calculates damage to each available player target (including modifiers)
   * The enemy unit group that can deal the highest damage to any single target gets to attack next
   * Dynamic recalculation each action based on remaining alive enemies
   */
  public selectNextEnemy(state: UnifiedBattleState): Enemy | null {
    const availableEnemies = state.enemies.filter((enemy, index) => 
      this.canEnemyAttack(enemy) && 
      !state.unitsActedThisTurn.enemies.has(index)
    );

    if (availableEnemies.length === 0) {
      return null;
    }

    const alivePlayers = state.playerGroups.filter(group => group.totalHealth > 0);
    if (alivePlayers.length === 0) {
      return null;
    }

    // Find the enemy unit group that can deal the highest damage to any single target
    let bestEnemy: Enemy | null = null;
    let maxSingleTargetDamage = 0;

    for (const enemy of availableEnemies) {
      const enemyMaxSingleTargetDamage = this.calculateEnemyMaxDamage(enemy, alivePlayers);
      
      if (enemyMaxSingleTargetDamage > maxSingleTargetDamage) {
        maxSingleTargetDamage = enemyMaxSingleTargetDamage;
        bestEnemy = enemy;
      }
    }

    return bestEnemy;
  }

  /**
   * Calculate the maximum damage a player unit can deal to its best target
   * Checks damage to each available enemy and returns the highest single-target damage
   */
  private calculatePlayerMaxDamage(player: BattlePlayerGroup, enemies: Enemy[]): number {
    let maxSingleTargetDamage = 0;

    for (const enemy of enemies) {
      const damageToThisTarget = this.calculatePlayerDamageToEnemy(player, enemy);
      maxSingleTargetDamage = Math.max(maxSingleTargetDamage, damageToThisTarget);
    }

    return maxSingleTargetDamage;
  }

  /**
   * Calculate the maximum damage an enemy can deal to its best target
   * Checks damage to each available player and returns the highest single-target damage
   */
  private calculateEnemyMaxDamage(enemy: Enemy, players: BattlePlayerGroup[]): number {
    let maxSingleTargetDamage = 0;

    for (const player of players) {
      const damageToThisTarget = this.calculateEnemyDamageToPlayer(enemy, player);
      maxSingleTargetDamage = Math.max(maxSingleTargetDamage, damageToThisTarget);
    }

    return maxSingleTargetDamage;
  }

  /**
   * Calculate damage a player unit group deals to a specific enemy
   */
  private calculatePlayerDamageToEnemy(player: BattlePlayerGroup, enemy: Enemy): DamageBreakdown {
    const baseDamage = player.count * player.unit.strength;
    let potentialDamage = baseDamage;
    const damageBreakdown = this.createDamageBreakdown(baseDamage);

    // For infinite and mortal enemies, check for attack modifiers
    if ((enemy.type === 'infinite' || enemy.type === 'mortal') && enemy.unit) {
      const bestModifier = this.getBestAttackModifier(player, enemy.unit);
      
      if (bestModifier) {
        potentialDamage = this.applyAttackModifiers(baseDamage, bestModifier, damageBreakdown);
      }
    }

    // Cap damage based on enemy type and health
    if (enemy.type === 'mortal') {
      const enemyTotalHealth = enemy.unit.health * enemy.currentCount;
      potentialDamage = Math.min(potentialDamage, enemyTotalHealth);
    }
    
    damageBreakdown.finalDamage = potentialDamage;
    return damageBreakdown;
  }

  /**
   * Calculate damage an enemy deals to a specific player unit group
   */
  private calculateEnemyDamageToPlayer(enemy: Enemy, player: BattlePlayerGroup): DamageBreakdown {
    let potentialDamage: number;
    let damageBreakdown: DamageBreakdown;

    // Generic enemies always kill (infinite damage)
    if (enemy.type === 'generic') {
      potentialDamage = UNIFIED_BATTLE_CONSTANTS.GENERIC_ENEMY_DAMAGE;
      damageBreakdown = this.createDamageBreakdown(potentialDamage);
    } else if (enemy.type === 'infinite') {
      const baseDamage = player.totalHealth;
      damageBreakdown = this.createDamageBreakdown(baseDamage);
      const bestModifier = this.getBestEnemyAttackModifier(enemy.unit, player);
      
      if (bestModifier && bestModifier.value > 0) {
        potentialDamage = this.applyAttackModifiers(baseDamage, bestModifier, damageBreakdown);
      } else {
        potentialDamage = baseDamage;
      }
    } else if (enemy.type === 'mortal') {
      const baseDamage = enemy.currentCount * enemy.unit.strength;
      damageBreakdown = this.createDamageBreakdown(baseDamage);
      const bestModifier = this.getBestEnemyAttackModifier(enemy.unit, player);
      
      if (bestModifier && bestModifier.value > 0) {
        potentialDamage = this.applyAttackModifiers(baseDamage, bestModifier, damageBreakdown);
      } else {
        potentialDamage = baseDamage;
      }
    } else {
      potentialDamage = 0;
      damageBreakdown = this.createDamageBreakdown(0);
    }

    // Cap damage at target's remaining health
    damageBreakdown.finalDamage = Math.min(potentialDamage, player.totalHealth);

    return damageBreakdown;
  }

  /**
   * Get the best attack modifier a player unit has against an enemy unit
   */
  private getBestAttackModifier(player: BattlePlayerGroup, enemyUnit: Unit): AttackModifier | null {
    // Check if player has unit data with attack modifiers
    if (!player.unit || !player.unit.attack_modifiers || player.unit.attack_modifiers.length === 0) {
      return null;
    }

    let bestModifier = null;
    let bestModifierValue = 0;
    
    // Check each attack modifier against enemy unit types
    for (const modifier of player.unit.attack_modifiers) {
      // Check if enemy has the target type that this modifier applies to
      if (enemyUnit.unit_types && enemyUnit.unit_types.includes(modifier.target_type)) {
        bestModifierValue = Math.max(bestModifierValue, modifier.value);
        bestModifier = modifier;
      }
    }
    
    return bestModifier;
  }

  /**
   * Get the best attack modifier an enemy unit has against a player unit group
   */
  private getBestEnemyAttackModifier(enemyUnit: Unit, player: BattlePlayerGroup):  AttackModifier | null {
    // Check if player has unit data with attack modifiers
    if (!enemyUnit || !enemyUnit.attack_modifiers || enemyUnit.attack_modifiers.length === 0) {
      return null;
    }

    let bestModifier = null;
    let bestModifierValue = 0;
    
    // Check each attack modifier against enemy unit types
    for (const modifier of enemyUnit.attack_modifiers) {
      // Check if enemy has the target type that this modifier applies to
      if (player.unit.unit_types && player.unit.unit_types.includes(modifier.target_type)) {
        bestModifierValue = Math.max(bestModifierValue, modifier.value);
        bestModifier = modifier;
      }
    }
    
    return bestModifier;
  }

  /**
   * Get all enemies that can currently attack
   */
  private getAttackableEnemies(state: UnifiedBattleState): Enemy[] {
    return state.enemies.filter(enemy => this.canEnemyAttack(enemy));
  }

  /**
   * Check if an enemy can currently attack
   */
  private canEnemyAttack(enemy: Enemy): boolean {
    switch (enemy.type) {
      case 'generic':
        return true; // Generic enemies always attack
      case 'infinite':
        return true; // Infinite enemies always attack
      case 'mortal':
        return enemy.currentCount > 0; // Mortal enemies attack if they have units left
      default:
        return false;
    }
  }

  /**
   * Create a DamageBreakdown object with base damage set
   */
  private createDamageBreakdown(baseDamage: number): DamageBreakdown {
    return {
      baseDamage,
      bonusDamage: 0,
      finalDamage: baseDamage,
      appliedModifiers: []
    } as DamageBreakdown;
  }

  /**
   * Apply attack modifiers to base damage and update breakdown
   */
  private applyAttackModifiers(baseDamage: number, modifier: AttackModifier, damageBreakdown: DamageBreakdown): number {
    const multiplier = 1 + (modifier.value / 100);
    const finalDamage = Math.floor(baseDamage * multiplier);
    
    damageBreakdown.bonusDamage = Math.round(baseDamage * (modifier.value / 100));
    damageBreakdown.appliedModifiers = [modifier];
    
    return finalDamage;
  }

  /**
   * Select the best target for a player unit group
   * Returns the enemy that this player deals the most damage to
   */
  public selectBestTargetForPlayer(player: BattlePlayerGroup, enemies: Enemy[]): Enemy | null {
    if (enemies.length === 0) return null;

    let bestTarget: Enemy | null = null;
    let maxDamage = 0;

    for (const enemy of enemies) {
      const damage = this.calculatePlayerDamageToEnemy(player, enemy);
      if (damage > maxDamage) {
        maxDamage = damage;
        bestTarget = enemy;
      }
    }

    return bestTarget;
  }

  /**
   * Select the best target for an enemy unit
   * Returns the player unit group that this enemy deals the most damage to
   */
  public selectBestTargetForEnemy(enemy: Enemy, players: BattlePlayerGroup[]): BattlePlayerGroup | null {
    if (players.length === 0) return null;

    let bestTarget: BattlePlayerGroup | null = null;
    let maxDamage = 0;

    for (const player of players) {
      const damage = this.calculateEnemyDamageToPlayer(enemy, player);
      if (damage > maxDamage) {
        maxDamage = damage;
        bestTarget = player;
      }
    }

    return bestTarget;
  }
}