/**
 * Infinite Enemy Handler
 * 
 * Handles specific enemy units with unlimited quantity (immortal enemies).
 * These enemies have specific stats, attack modifiers, and targeting preferences
 * but never get eliminated from battle.
 */

import { 
  EnemyTypeHandler, 
  InfiniteEnemy, 
  BattlePlayerGroup, 
  AttackResult 
} from '../types';
import { CombatAction } from '../../../types/battleSimulation';

export class InfiniteEnemyHandler implements EnemyTypeHandler<InfiniteEnemy> {
  
  /**
   * Infinite enemies can always attack (they never get eliminated)
   */
  public canAttack(enemy: InfiniteEnemy): boolean {
    return true;
  }

  /**
   * Calculate damage an infinite enemy deals to a player unit group
   */
  public calculateDamage(enemy: InfiniteEnemy, target: BattlePlayerGroup): number {
    const baseDamage = enemy.unit.strength;
    
    // Check for attack modifiers against the target
    const modifierPercentage = this.getBestAttackModifier(enemy, target);
    
    if (modifierPercentage !== 0) {
      // Apply percentage modifier (e.g., 50 = 50% increase)
      // Use original strength for attack modifier calculations
      const baseStrengthForModifiers = enemy.unit.originalStrength || enemy.unit.strength;
      const modifierDamage = Math.floor(baseStrengthForModifiers * (modifierPercentage / 100));
      return baseDamage + modifierDamage;
    }
    
    return baseDamage;
  }

  /**
   * Process an infinite enemy attack against a player unit group
   */
  public processAttack(enemy: InfiniteEnemy, target: BattlePlayerGroup): AttackResult {
    const actualDamage = this.calculateActualDamage(enemy, target);
    const actualDamageDealt = actualDamage.healthLost;
    
    const targetEliminated = actualDamageDealt >= target.totalHealth;
    
    // Create combat action (will be populated by the caller with turn info)
    const combatAction: CombatAction = {
      turn: 0, // Will be set by caller
      attacker: this.getDisplayName(enemy),
      target: target.unit.name,
      action: `attack ${target.unit.name} for ${actualDamageDealt} damage${targetEliminated ? ' (ELIMINATED)' : ''}`,
      damageDealt: actualDamageDealt,
      eliminated: targetEliminated
    };

    return {
      damageDealt: actualDamageDealt,
      targetEliminated,
      attackerEliminated: false, // Infinite enemies never get eliminated
      combatAction
    };
  }

  /**
   * Infinite enemies are never eliminated
   */
  public isEliminated(enemy: InfiniteEnemy): boolean {
    return false;
  }

  /**
   * Get display name for combat logs
   */
  public getDisplayName(enemy: InfiniteEnemy): string {
    return enemy.unit.name;
  }

  /**
   * Get strength for damage calculations and comparisons
   */
  public getStrength(enemy: InfiniteEnemy): number {
    return enemy.unit.strength;
  }

  /**
   * Calculate actual damage to be applied to player unit group
   */
  public calculateActualDamage(enemy: InfiniteEnemy, target: BattlePlayerGroup): {
    healthLost: number;
    unitsKilled: number;
  } {
    const totalDamage = this.calculateDamage(enemy, target);
    
    // Calculate how many units are killed
    const unitsKilled = Math.min(
      Math.floor(totalDamage / target.unit.health),
      target.count
    );
    
    // Calculate actual health lost
    const healthLost = Math.min(totalDamage, target.totalHealth);
    
    return {
      healthLost,
      unitsKilled
    };
  }

  /**
   * Get the best attack modifier against a target
   */
  private getBestAttackModifier(enemy: InfiniteEnemy, target: BattlePlayerGroup): number {
    if (!enemy.unit.attack_modifiers || enemy.unit.attack_modifiers.length === 0) {
      return 0;
    }

    // If target has unit data with unit_types, check for modifiers
    if (target.unit?.unit_types) {
      let bestModifier = 0;
      
      for (const modifier of enemy.unit.attack_modifiers) {
        // Check if modifier applies to any of the target's unit types
        if (target.unit.unit_types.includes(modifier.target_type) && 
            modifier.modifier_type === 'Strength') {
          bestModifier = Math.max(bestModifier, modifier.value);
        }
      }
      
      return bestModifier;
    }
    
    // If no unit data available, return 0 (no modifiers can be applied)
    return 0;
  }

  /**
   * Check if this enemy has targeting preferences based on attack modifiers
   */
  public hasTargetPreference(enemy: InfiniteEnemy): boolean {
    return enemy.unit.attack_modifiers && enemy.unit.attack_modifiers.length > 0;
  }

  /**
   * Get targeting priority score for a specific target
   * Higher scores indicate better targets for this enemy
   */
  public getTargetPriority(enemy: InfiniteEnemy, target: BattlePlayerGroup): number {
    const basePriority = 1.0;
    const modifier = this.getBestAttackModifier(enemy, target);
    
    if (modifier > 0) {
      // Targets with damage bonuses get higher priority
      return basePriority + (modifier / 100);
    }
    
    return basePriority;
  }

  /**
   * Check if this enemy can be counter-attacked
   * Infinite enemies can be attacked but never eliminated
   */
  public canBeCounterAttacked(enemy: InfiniteEnemy): boolean {
    return true;
  }

  /**
   * Get enemy health (for display/calculation purposes)
   */
  public getHealth(enemy: InfiniteEnemy): number {
    return enemy.unit.health;
  }

  /**
   * Get enemy unit types (for targeting calculations)
   */
  public getUnitTypes(enemy: InfiniteEnemy): string[] {
    return enemy.unit.unit_types || [];
  }

  /**
   * Get enemy attack modifiers (for damage calculations)
   */
  public getAttackModifiers(enemy: InfiniteEnemy): any[] {
    return enemy.unit.attack_modifiers || [];
  }

  /**
   * Get enemy type identifier
   */
  public getEnemyType(): 'infinite' {
    return 'infinite';
  }

  /**
   * Calculate maximum potential damage this enemy can deal to the best target
   * Used for attack order prioritization - this should be the damage to the single best target,
   * not total damage to all targets (that's calculated by the attack selector)
   */
  public getMaxPotentialDamage(enemy: InfiniteEnemy, availableTargets?: BattlePlayerGroup[]): number {
    const baseDamage = enemy.unit.strength;
    
    if (!availableTargets || availableTargets.length === 0) {
      // Return base damage if no targets specified
      return baseDamage;
    }
    
    // Find the target this enemy would deal the most damage to
    let maxDamage = baseDamage;
    
    for (const target of availableTargets) {
      const damageToThisTarget = this.calculateDamage(enemy, target);
      maxDamage = Math.max(maxDamage, damageToThisTarget);
    }
    
    return maxDamage;
  }

  /**
   * Calculate total potential damage this enemy could deal to all available targets
   * Used by attack selector to determine which unit should attack next
   */
  public calculateTotalPotentialDamage(enemy: InfiniteEnemy, availableTargets: BattlePlayerGroup[]): number {
    let totalDamage = 0;
    
    for (const target of availableTargets) {
      totalDamage += this.calculateDamage(enemy, target);
    }
    
    return totalDamage;
  }
}