/**
 * Enemy Factory
 * 
 * Factory class for creating and managing enemy type handlers.
 * Provides a centralized way to instantiate the correct handler
 * for each enemy type and manage handler lifecycle.
 */

import { EnemyTypeHandler, Enemy, EnemyType } from '../types';
import { GenericEnemyHandler } from './GenericEnemyHandler';
import { InfiniteEnemyHandler } from './InfiniteEnemyHandler';
import { MortalEnemyHandler } from './MortalEnemyHandler';

export class EnemyFactory {
  private static instance: EnemyFactory;
  private handlers: Map<EnemyType, EnemyTypeHandler> = new Map();

  private constructor() {
    this.initializeHandlers();
  }

  /**
   * Get singleton instance of the factory
   */
  public static getInstance(): EnemyFactory {
    if (!EnemyFactory.instance) {
      EnemyFactory.instance = new EnemyFactory();
    }
    return EnemyFactory.instance;
  }

  /**
   * Initialize all enemy type handlers
   */
  private initializeHandlers(): void {
    this.handlers.set('generic', new GenericEnemyHandler());
    this.handlers.set('infinite', new InfiniteEnemyHandler());
    this.handlers.set('mortal', new MortalEnemyHandler());
  }

  /**
   * Get the appropriate handler for an enemy type
   */
  public getHandler(enemyType: EnemyType): EnemyTypeHandler {
    const handler = this.handlers.get(enemyType);
    if (!handler) {
      throw new Error(`No handler found for enemy type: ${enemyType}`);
    }
    return handler;
  }

  /**
   * Get the appropriate handler for a specific enemy instance
   */
  public getHandlerForEnemy(enemy: Enemy): EnemyTypeHandler {
    return this.getHandler(enemy.type);
  }

  /**
   * Check if a handler exists for the given enemy type
   */
  public hasHandler(enemyType: EnemyType): boolean {
    return this.handlers.has(enemyType);
  }

  /**
   * Get all supported enemy types
   */
  public getSupportedEnemyTypes(): EnemyType[] {
    return Array.from(this.handlers.keys());
  }

  /**
   * Validate that all required handlers are available
   */
  public validateHandlers(): { isValid: boolean; missingTypes: EnemyType[] } {
    const requiredTypes: EnemyType[] = ['generic', 'infinite', 'mortal'];
    const missingTypes: EnemyType[] = [];

    for (const type of requiredTypes) {
      if (!this.hasHandler(type)) {
        missingTypes.push(type);
      }
    }

    return {
      isValid: missingTypes.length === 0,
      missingTypes
    };
  }

  /**
   * Create a new enemy configuration with proper type validation
   */
  public createEnemyConfiguration(enemyData: any): Enemy {
    if (!enemyData.type || typeof enemyData.type !== 'string') {
      throw new Error('Enemy configuration must have a valid type');
    }

    const enemyType = enemyData.type as EnemyType;
    
    if (!this.hasHandler(enemyType)) {
      throw new Error(`Unsupported enemy type: ${enemyType}`);
    }

    // Validate and create enemy configuration based on type
    switch (enemyType) {
      case 'generic':
        return this.createGenericEnemy(enemyData);
      case 'infinite':
        return this.createInfiniteEnemy(enemyData);
      case 'mortal':
        return this.createMortalEnemy(enemyData);
      default:
        throw new Error(`Unknown enemy type: ${enemyType}`);
    }
  }

  /**
   * Create a generic enemy configuration
   */
  private createGenericEnemy(data: any): Enemy {
    if (!data.name || typeof data.name !== 'string') {
      throw new Error('Generic enemy must have a name');
    }

    return {
      type: 'generic',
      name: data.name,
      groupIndex: data.groupIndex || 0
    };
  }

  /**
   * Create an infinite enemy configuration
   */
  private createInfiniteEnemy(data: any): Enemy {
    if (!data.unit || typeof data.unit !== 'object') {
      throw new Error('Infinite enemy must have unit data');
    }

    // Validate required unit properties
    const unit = data.unit;
    if (!unit.name || typeof unit.name !== 'string') {
      throw new Error('Infinite enemy unit must have a name');
    }
    if (typeof unit.strength !== 'number' || unit.strength < 0) {
      throw new Error('Infinite enemy unit must have valid strength');
    }
    if (typeof unit.health !== 'number' || unit.health <= 0) {
      throw new Error('Infinite enemy unit must have valid health');
    }

    return {
      type: 'infinite',
      unit: {
        ...unit,
        unit_types: unit.unit_types || [],
        attack_modifiers: unit.attack_modifiers || []
      },
      groupIndex: data.groupIndex || 0
    };
  }

  /**
   * Create a mortal enemy configuration
   */
  private createMortalEnemy(data: any): Enemy {
    if (!data.unit || typeof data.unit !== 'object') {
      throw new Error('Mortal enemy must have unit data');
    }
    if (typeof data.count !== 'number' || data.count <= 0) {
      throw new Error('Mortal enemy must have a positive count');
    }

    // Validate required unit properties
    const unit = data.unit;
    if (!unit.name || typeof unit.name !== 'string') {
      throw new Error('Mortal enemy unit must have a name');
    }
    if (typeof unit.strength !== 'number' || unit.strength < 0) {
      throw new Error('Mortal enemy unit must have valid strength');
    }
    if (typeof unit.health !== 'number' || unit.health <= 0) {
      throw new Error('Mortal enemy unit must have valid health');
    }

    return {
      type: 'mortal',
      unit: {
        ...unit,
        unit_types: unit.unit_types || [],
        attack_modifiers: unit.attack_modifiers || []
      },
      count: data.count,
      currentCount: data.currentCount || data.count,
      groupIndex: data.groupIndex || 0
    };
  }

  /**
   * Reset all handlers to initial state (if needed)
   */
  public resetHandlers(): void {
    // Handlers are stateless, but this method is available for future use
    // if any handlers need state reset functionality
  }

  /**
   * Get handler statistics for debugging/monitoring
   */
  public getHandlerStats(): Record<EnemyType, any> {
    const stats: Record<string, any> = {};

    for (const [type, handler] of this.handlers) {
      stats[type] = {
        type: handler.getEnemyType(),
        available: true
      };
    }

    return stats;
  }

  /**
   * Clone an enemy configuration for battle reuse
   */
  public cloneEnemy(enemy: Enemy): Enemy {
    switch (enemy.type) {
      case 'generic':
        return { ...enemy };
      case 'infinite':
        return {
          ...enemy,
          unit: { ...enemy.unit }
        };
      case 'mortal':
        return {
          ...enemy,
          unit: { ...enemy.unit },
          // Reset current count to original count for fresh battles
          currentCount: enemy.count
        };
      default:
        throw new Error(`Cannot clone unknown enemy type: ${(enemy as any).type}`);
    }
  }

  /**
   * Batch create multiple enemy configurations
   */
  public createMultipleEnemies(enemyDataArray: any[]): Enemy[] {
    const enemies: Enemy[] = [];
    
    for (let i = 0; i < enemyDataArray.length; i++) {
      try {
        const enemy = this.createEnemyConfiguration({
          ...enemyDataArray[i],
          groupIndex: enemyDataArray[i].groupIndex || i
        });
        enemies.push(enemy);
      } catch (error) {
        throw new Error(`Error creating enemy at index ${i}: ${error}`);
      }
    }

    return enemies;
  }
}