/**
 * Improved Damage Optimization Service
 * Streamlined service for the new damage optimization page workflow
 * Follows the same algorithm pattern as damageOptimizer.ts but with simplified interface
 */

import { Unit, ArmyComposition, OptimizationConstraints } from '../types/units';
import { UnifiedBattleSimulation } from './unified-battle-simulation/UnifiedBattleSimulation';
import { UnifiedBattleConfiguration, Enemy } from './unified-battle-simulation/types';
import { StackingOptimizer } from './stackingOptimizer';
import {
  ImprovedOptimizationConfig,
  ImprovedOptimizationProgress,
  ImprovedOptimizationResult,
  ImprovedOptimizationResults,
  ImprovedBattleAnalysis,
  OptimizationEnemyInput
} from '../types/improvedDamageOptimization';

/**
 * Main service for the improved damage optimization page
 */
export class ImprovedDamageOptimizer {
  private stackingOptimizer: StackingOptimizer;
  private unifiedBattleService: UnifiedBattleSimulation;

  constructor(units: Unit[]) {
    this.stackingOptimizer = new StackingOptimizer(units);
    this.unifiedBattleService = new UnifiedBattleSimulation();
  }

  /**
   * Run the complete optimization workflow
   */
  async optimize(config: ImprovedOptimizationConfig, units: Unit[]): Promise<ImprovedOptimizationResults> {
    const startTime = performance.now();
    const maxTimeoutMs = 120000; // 2 minutes maximum

    console.log(' Starting improved damage optimization');
    console.log(` Enemies: ${config.enemies.length}, Available units: ${config.unitsConfig.availableUnitNames.length}`);

    // Report initial progress
    this.reportProgress(config, {
      phase: 'initializing',
      progress: 0,
      message: 'Initializing optimization...',
      elapsedMs: 0
    });

    // Validate configuration
    this.validateConfig(config);

    // Convert enemy input to unified format
    const unifiedEnemies = this.convertEnemyInput(config.enemies, units);

    // Report generation phase
    this.reportProgress(config, {
      phase: 'generating',
      progress: 10,
      message: 'Generating army combinations...',
      elapsedMs: performance.now() - startTime
    });

    // Generate army combinations using systematic approach (same as damageOptimizer)
    const armyCombinations = await this.generateArmyCombinations(config, units);

    console.log(` Generated ${armyCombinations.length} army combinations to evaluate`);

    // Report evaluation phase
    this.reportProgress(config, {
      phase: 'evaluating',
      progress: 20,
      message: 'Evaluating army combinations...',
      combinationsGenerated: armyCombinations.length,
      totalCombinations: armyCombinations.length,
      combinationsEvaluated: 0,
      totalToEvaluate: armyCombinations.length,
      elapsedMs: performance.now() - startTime
    });

    // Evaluate each combination
    const results: ImprovedOptimizationResult[] = [];
    let combinationsEvaluated = 0;

    for (let i = 0; i < armyCombinations.length; i++) {
      // Check for cancellation
      this.checkCancellation(config);

      // Check for timeout
      const currentTime = performance.now();
      if (currentTime - startTime > maxTimeoutMs) {
        console.warn(` Optimization timeout after ${(currentTime - startTime) / 1000}s`);
        break;
      }

      const army = armyCombinations[i];

      try {
        const result = await this.evaluateArmyComposition(army, unifiedEnemies, units);
        results.push(result);
        combinationsEvaluated++;
      } catch (error) {
        console.warn(' Failed to evaluate army composition:', error);
      }

      // Yield control to UI
      if (combinationsEvaluated % 3 === 0) {
        await new Promise(resolve => setTimeout(resolve, 0));
      }

      // Report progress
      if (combinationsEvaluated % 10 === 0 || combinationsEvaluated === armyCombinations.length) {
        const elapsedMs = performance.now() - startTime;
        const progressPercent = 20 + Math.floor((combinationsEvaluated / armyCombinations.length) * 60);
        const estimatedRemainingMs = combinationsEvaluated > 0
          ? (elapsedMs / combinationsEvaluated) * (armyCombinations.length - combinationsEvaluated)
          : undefined;

        this.reportProgress(config, {
          phase: 'evaluating',
          progress: progressPercent,
          message: `Evaluating combinations... (${combinationsEvaluated}/${armyCombinations.length})`,
          combinationsGenerated: armyCombinations.length,
          totalCombinations: armyCombinations.length,
          combinationsEvaluated,
          totalToEvaluate: armyCombinations.length,
          elapsedMs,
          estimatedRemainingMs
        });
      }
    }

    // Report finalizing phase
    this.reportProgress(config, {
      phase: 'finalizing',
      progress: 90,
      message: 'Finalizing results...',
      elapsedMs: performance.now() - startTime
    });

    // Sort results by damage output
    const rankedResults = results.sort((a, b) => {
      const damageDiff = b.averageDamagePerBattle - a.averageDamagePerBattle;
      if (Math.abs(damageDiff) > 0.01) return damageDiff;
      return b.damageEfficiencyScore - a.damageEfficiencyScore;
    });

    const endTime = performance.now();
    const optimizationTimeMs = endTime - startTime;

    console.log(` Optimization complete: ${rankedResults.length} results in ${optimizationTimeMs.toFixed(2)}ms`);

    // Report completion
    this.reportProgress(config, {
      phase: 'finalizing',
      progress: 100,
      message: 'Optimization complete!',
      combinationsGenerated: armyCombinations.length,
      totalCombinations: armyCombinations.length,
      combinationsEvaluated,
      totalToEvaluate: armyCombinations.length,
      elapsedMs: optimizationTimeMs
    });

    return {
      rankedResults,
      combinationsEvaluated,
      optimizationTimeMs,
      wasTruncated: armyCombinations.length > (config.maxCombinations || 50),
      enemyConfig: config.enemies,
      unitsConfig: config.unitsConfig
    };
  }

  /**
   * Generate army combinations using the proven stacking algorithm approach
   */
  private async generateArmyCombinations(
    config: ImprovedOptimizationConfig,
    units: Unit[]
  ): Promise<ArmyComposition[]> {
    const maxCombinations = config.maxCombinations || 50;
    const availableUnitNames = config.unitsConfig.availableUnitNames;

    // Generate unit combinations (same logic as damageOptimizer)
    const unitCombinations = this.generateUnitCombinations(availableUnitNames, maxCombinations);
    const results: ArmyComposition[] = [];

    // Test each combination using stacking algorithm
    for (const combination of unitCombinations) {
      try {
        const army = await this.testCombinationWithStackingAlgorithm(combination, config, units);
        if (army) {
          results.push(army);
        }
      } catch (error) {
        console.warn(` Failed to test combination [${combination.join(', ')}]:`, error);
      }
    }

    return results;
  }

  /**
   * Generate unit combinations (same logic as damageOptimizer)
   */
  private generateUnitCombinations(unitNames: string[], maxCombinations: number): string[][] {
    const totalPossible = Math.pow(2, unitNames.length) - 1;

    if (totalPossible <= maxCombinations) {
      // Generate all combinations
      const allCombinations: string[][] = [];
      for (let i = 1; i <= totalPossible; i++) {
        const combination: string[] = [];
        for (let j = 0; j < unitNames.length; j++) {
          if (i & (1 << j)) {
            combination.push(unitNames[j]);
          }
        }
        allCombinations.push(combination);
      }
      return allCombinations.sort((a, b) => b.length - a.length);
    } else {
      // Use limited smart combinations
      return this.generateLimitedCombinations(unitNames, maxCombinations);
    }
  }

  /**
   * Generate limited combinations (same logic as damageOptimizer)
   */
  private generateLimitedCombinations(unitNames: string[], maxCombinations: number): string[][] {
    const combinations: string[][] = [];

    // Start with all units
    combinations.push([...unitNames]);

    // Remove one unit at a time
    for (let i = 0; i < unitNames.length && combinations.length < maxCombinations; i++) {
      const combination = unitNames.filter((_, index) => index !== i);
      combinations.push(combination);
    }

    // Remove two units at a time
    for (let i = 0; i < unitNames.length && combinations.length < maxCombinations; i++) {
      for (let j = i + 1; j < unitNames.length && combinations.length < maxCombinations; j++) {
        const combination = unitNames.filter((_, index) => index !== i && index !== j);
        if (combination.length > 0) {
          combinations.push(combination);
        }
      }
    }

    return combinations;
  }

  /**
   * Test combination with stacking algorithm (same approach as damageOptimizer)
   */
  private async testCombinationWithStackingAlgorithm(
    unitCombination: string[],
    config: ImprovedOptimizationConfig,
    allUnits: Unit[]
  ): Promise<ArmyComposition | null> {
    try {
      const stackingConstraints: OptimizationConstraints = {
        leadershipBudget: config.unitsConfig.leadershipBudget,
        dominanceBudget: config.unitsConfig.dominanceBudget,
        mercenaryLimits: config.unitsConfig.mercenaryLimits,
        availableUnits: unitCombination
      };

      const result = await this.stackingOptimizer.optimizeArmy(stackingConstraints);

      if (result.compositions && result.compositions.length > 0) {
        return result.compositions[0];
      }

      return null;
    } catch (error) {
      console.warn(`Failed to test combination [${unitCombination.join(', ')}]:`, error);
      return null;
    }
  }

  /**
   * Evaluate a single army composition
   */
  private async evaluateArmyComposition(
    army: ArmyComposition,
    enemies: Enemy[],
    units: Unit[]
  ): Promise<ImprovedOptimizationResult> {
    // Run best case scenario (player goes first)
    const bestCaseConfig: UnifiedBattleConfiguration = {
      playerArmy: {
        stackingOrder: JSON.parse(JSON.stringify(army.stackingOrder)),
        totalStrength: army.totalStrength,
        totalHealth: army.totalHealth
      },
      enemies: JSON.parse(JSON.stringify(enemies)),
      playerGoesFirst: true
    };

    const bestCaseResult = this.unifiedBattleService.simulateBattle(bestCaseConfig);

    // Run worst case scenario (enemy goes first)
    const worstCaseConfig: UnifiedBattleConfiguration = {
      playerArmy: {
        stackingOrder: JSON.parse(JSON.stringify(army.stackingOrder)),
        totalStrength: army.totalStrength,
        totalHealth: army.totalHealth
      },
      enemies: JSON.parse(JSON.stringify(enemies)),
      playerGoesFirst: false
    };

    const worstCaseResult = this.unifiedBattleService.simulateBattle(worstCaseConfig);

    const battleAnalysis: ImprovedBattleAnalysis = {
      bestCase: bestCaseResult,
      worstCase: worstCaseResult
    };

    // Calculate metrics
    const totalSilverCost = this.calculateSilverCost(army, units);
    const totalFoodConsumption = this.calculateFoodConsumption(army, units);
    const averageDamagePerBattle = (bestCaseResult.totalDamageDealt + worstCaseResult.totalDamageDealt) / 2;
    const totalResourceCost = army.totalLeadershipCost + army.totalDominanceCost + totalSilverCost;
    const damageEfficiencyScore = totalResourceCost > 0 ? averageDamagePerBattle / totalResourceCost : 0;

    // Create battle summary
    const battleSummary = {
      bestCaseOutcome: bestCaseResult.outcome.result,
      worstCaseOutcome: worstCaseResult.outcome.result,
      averageTurns: (bestCaseResult.battleDuration + worstCaseResult.battleDuration) / 2
    };

    return {
      armyComposition: army,
      battleAnalysis,
      totalSilverCost,
      totalFoodConsumption,
      averageDamagePerBattle,
      damageEfficiencyScore,
      battleSummary
    };
  }

  /**
   * Convert enemy input to unified battle system format
   */
  private convertEnemyInput(enemyInputs: OptimizationEnemyInput[], units: Unit[]): Enemy[] {
    const unitsByName = new Map(units.map(unit => [unit.name, unit]));

    return enemyInputs.map((enemyInput, index) => {
      if (enemyInput.name === 'Generic' || !enemyInput.strength || !enemyInput.health) {
        // Generic enemy
        return {
          type: 'generic' as const,
          name: enemyInput.name === 'Generic' ? `Generic Enemy ${index + 1}` : enemyInput.name,
          groupIndex: index
        };
      } else {
        // Specific enemy unit
        const unit = unitsByName.get(enemyInput.name) || {
          name: enemyInput.name,
          strength: enemyInput.strength!,
          health: enemyInput.health!,
          unit_types: enemyInput.unit_types || [],
          attack_modifiers: enemyInput.attack_modifiers || []
        } as Unit;

        if (enemyInput.isFinite) {
          // Mortal enemy
          return {
            type: 'mortal' as const,
            unit,
            count: enemyInput.count,
            currentCount: enemyInput.count,
            groupIndex: index
          };
        } else {
          // Infinite enemy
          return {
            type: 'infinite' as const,
            unit,
            groupIndex: index
          };
        }
      }
    });
  }

  /**
   * Calculate silver cost (same logic as damageOptimizer)
   */
  private calculateSilverCost(army: ArmyComposition, units: Unit[]): number {
    const unitsByName = new Map(units.map(unit => [unit.name, unit]));
    let totalSilverCost = 0;

    for (const [unitName, quantity] of Object.entries(army.units)) {
      const unit = unitsByName.get(unitName);
      if (unit) {
        totalSilverCost += (unit.revival_cost_silver ?? 0) * quantity;
      }
    }

    return totalSilverCost;
  }

  /**
   * Calculate food consumption (same logic as damageOptimizer)
   */
  private calculateFoodConsumption(army: ArmyComposition, units: Unit[]): number {
    const unitsByName = new Map(units.map(unit => [unit.name, unit]));
    let totalFoodConsumption = 0;

    for (const [unitName, quantity] of Object.entries(army.units)) {
      const unit = unitsByName.get(unitName);
      if (unit) {
        totalFoodConsumption += (unit.food_consumption ?? 0) * quantity;
      }
    }

    return totalFoodConsumption;
  }

  /**
   * Report progress callback
   */
  private reportProgress(config: ImprovedOptimizationConfig, progress: ImprovedOptimizationProgress): void {
    if (config.onProgress) {
      config.onProgress(progress);
    }
  }

  /**
   * Check for cancellation
   */
  private checkCancellation(config: ImprovedOptimizationConfig): void {
    if (config.signal?.aborted) {
      throw new Error('Operation was cancelled by user');
    }
  }

  /**
   * Validate configuration
   */
  private validateConfig(config: ImprovedOptimizationConfig): void {
    if (!config.enemies || config.enemies.length === 0) {
      throw new Error('At least one enemy must be specified');
    }

    if (!config.unitsConfig.availableUnitNames || config.unitsConfig.availableUnitNames.length === 0) {
      throw new Error('At least one unit type must be available for optimization');
    }

    if (config.unitsConfig.leadershipBudget < 0) {
      throw new Error('Leadership budget cannot be negative');
    }

    if (config.unitsConfig.dominanceBudget < 0) {
      throw new Error('Dominance budget cannot be negative');
    }

    for (const enemy of config.enemies) {
      if (enemy.count < 1) {
        throw new Error('Enemy count must be at least 1');
      }
    }
  }
}