/**
 * Battle State Management
 * 
 * Centralized state management for unified battle simulation.
 * Handles battle state initialization, updates, and validation according to proper battle mechanics.
 */

import { 
  UnifiedBattleState, 
  UnifiedBattleConfiguration, 
  BattlePlayerGroup, 
  Enemy, 
  ActionTracking,
  AttackPlan,
  AttackResult,
  UNIFIED_BATTLE_CONSTANTS
} from './types';
import { CombatAction } from '../../types/battleSimulation';
import { BattleLogger, BattleLogEntry } from './mechanics/BattleLogger';

export class BattleState {
  private state: UnifiedBattleState;
  private battleLogger: BattleLogger;

  constructor(config: UnifiedBattleConfiguration) {
    this.state = this.initializeBattleState(config);
    this.battleLogger = new BattleLogger();
  }

  /**
   * Initialize battle state from configuration
   */
  private initializeBattleState(config: UnifiedBattleConfiguration): UnifiedBattleState {
    return {
      currentTurn: 1,
      currentAction: 0,
      playerGroups: config.playerArmy.stackingOrder.map(group => ({ ...group })),
      enemies: config.enemies.map(enemy => ({ ...enemy })),
      unitsActedThisTurn: {
        players: new Set<string>(),
        enemies: new Set<number>()
      },
      combatLog: [],
      battleEnded: false,
      totalDamageDealt: 0
    };
  }

  /**
   * Deep clone the battle state to avoid reference sharing
   */
  private deepCloneState(state: UnifiedBattleState): UnifiedBattleState {
    return {
      ...state,
      // Deep copy the Sets to avoid shared references
      unitsActedThisTurn: {
        players: new Set(state.unitsActedThisTurn.players),
        enemies: new Set(state.unitsActedThisTurn.enemies)
      },
      // Deep copy arrays to avoid shared references
      playerGroups: state.playerGroups.map(group => ({ ...group })),
      enemies: state.enemies.map(enemy => ({ ...enemy })),
      combatLog: [...state.combatLog]
    };
  }

  /**
   * Get current battle state (read-only access)
   */
  public getBattleState(): Readonly<UnifiedBattleState> {
    return this.deepCloneState(this.state);
  }

  /**
   * Apply a state update from pure functions (like TurnManager)
   * Replaces the current state with the updated state
   */
  public applyStateUpdate(newState: UnifiedBattleState): void {
    // Validate that the new state has the required structure
    if (!newState || typeof newState.currentTurn !== 'number' || typeof newState.currentAction !== 'number') {
      throw new Error('Invalid state update: missing required fields');
    }
    
    this.state = newState;
  }

  /**
   * Get alive player groups (units with health > 0)
   */
  public getAlivePlayerGroups(): BattlePlayerGroup[] {
    return this.state.playerGroups.filter(group => group.totalHealth > 0);
  }

  /**
   * Get attackable enemies (can still attack based on enemy type)
   */
  public getAttackableEnemies(): Enemy[] {
    return this.state.enemies.filter(enemy => {
      switch (enemy.type) {
        case 'generic':
          return true; // Generic enemies always attack
        case 'infinite':
          return true; // Infinite enemies always attack
        case 'mortal':
          return enemy.currentCount > 0; // Mortal enemies attack if they have units left
        default:
          return false;
      }
    });
  }

  // Turn management methods removed - now handled by standalone TurnManager class

  /**
   * Update player group after taking damage
   */
  public updatePlayerGroup(groupName: string, newHealth: number, unitsLost: number = 0): void {
    const group = this.state.playerGroups.find(g => g.unit.name === groupName);
    if (group) {
      group.totalHealth = Math.max(0, newHealth);
      if (unitsLost > 0) {
        group.count = Math.max(0, group.count - unitsLost);
      }
    }
  }

  /**
   * Update mortal enemy after taking damage
   */
  public updateMortalEnemy(enemyIndex: number, unitsLost: number): void {
    const enemy = this.state.enemies[enemyIndex];
    if (enemy && enemy.type === 'mortal') {
      enemy.currentCount = Math.max(0, enemy.currentCount - unitsLost);
    }
  }

  /**
   * Apply complete attack plan including damage, combat log, marking as acted (unified pure function approach)
   * This is the most efficient way to apply all attack-related state changes in one operation
   */
  public applyCompleteAttack(state: UnifiedBattleState, attackPlan: AttackPlan): UnifiedBattleState {
    const newState = this.deepCloneState(state);
    
    // Create proper battle log entry using BattleLogger (centralized logging)
    const attackResult: AttackResult = {
      damageDealt: attackPlan.damageDealt,
      bonusDamageDealt: attackPlan.damageBreakdown?.bonusDamage,
      modifierName: !!attackPlan.damageBreakdown?.appliedModifiers ? attackPlan.damageBreakdown.appliedModifiers.length > 0 ? attackPlan.damageBreakdown.appliedModifiers[0].target_type : undefined : undefined ,
      targetEliminated: attackPlan.targetEliminated,
      attackerEliminated: false, // Not relevant for current mechanics
      combatAction: attackPlan.combatAction // Legacy field, will be replaced
    };
    
    const battleLogEntry: BattleLogEntry = this.isPlayerGroup(attackPlan.attacker)
      ? this.battleLogger.logPlayerAction(
          attackPlan.attacker as BattlePlayerGroup,
          attackPlan.target as Enemy,
          attackResult,
          state.currentTurn,
          state.currentAction,
          state
        )
      : this.battleLogger.logEnemyAction(
          attackPlan.attacker as Enemy,
          attackPlan.target as BattlePlayerGroup,
          attackResult,
          state.currentTurn,
          state.currentAction,
          state
        );
    
    // Add proper battle log entry to combat log (BattleLogEntry extends CombatAction)
    newState.combatLog.push(battleLogEntry);
    
    // Add damage to total
    newState.totalDamageDealt += attackPlan.damageDealt;
    
    // Mark the attacker as having acted this turn
    if (this.isPlayerGroup(attackPlan.attacker)) {
      // Player unit
      newState.unitsActedThisTurn.players.add(attackPlan.attacker.unit.name);
    } else {
      // Enemy unit - find its index by groupIndex
      const enemyIndex = newState.enemies.findIndex(e => e.groupIndex === (attackPlan.attacker as Enemy).groupIndex);
      if (enemyIndex >= 0) {
        newState.unitsActedThisTurn.enemies.add(enemyIndex);
      }
    }
    
    // Increment action counter
    newState.currentAction++;
    
    // Apply damage and elimination to target
    if (attackPlan.targetEliminated) {
      // Handle eliminated targets
      if (this.isPlayerGroup(attackPlan.target)) {
        // Target is player - eliminate player group
        const targetGroup = newState.playerGroups.find(g => g.unit.name === (attackPlan.target as BattlePlayerGroup).unit.name);
        if (targetGroup) {
          targetGroup.totalHealth = 0;
          targetGroup.count = 0;
        }
      } else {
        // Target is enemy - eliminate enemy (only applies to mortal enemies)
        if (attackPlan.target.type === 'mortal') {
          const enemyIndex = newState.enemies.findIndex(e => e.groupIndex === (attackPlan.target as Enemy).groupIndex);
          if (enemyIndex >= 0 && newState.enemies[enemyIndex].type === 'mortal') {
            (newState.enemies[enemyIndex] as any).currentCount = 0;
          }
        }
      }
    } else {
      // Apply damage to target (non-eliminated)
      if (this.isPlayerGroup(attackPlan.target)) {
        // Target is player - apply damage
        const targetGroup = newState.playerGroups.find(g => g.unit.name === (attackPlan.target as BattlePlayerGroup).unit.name);
        if (targetGroup && attackPlan.damageDealt > 0) {
          targetGroup.totalHealth = Math.max(0, targetGroup.totalHealth - attackPlan.damageDealt);
          // Calculate units lost based on damage
          const unitHealth = targetGroup.unit.health;
          const unitsLost = Math.floor(attackPlan.damageDealt / unitHealth);
          targetGroup.count = Math.max(0, targetGroup.count - unitsLost);
        }
      } else {
        // Target is enemy - apply damage (only applies to mortal enemies)
        if (attackPlan.target.type === 'mortal') {
          const enemyIndex = newState.enemies.findIndex(e => e.groupIndex === (attackPlan.target as Enemy).groupIndex);
          if (enemyIndex >= 0 && newState.enemies[enemyIndex].type === 'mortal') {
            const enemy = newState.enemies[enemyIndex] as any;
            const unitsLost = Math.floor(attackPlan.damageDealt / enemy.unit.health);
            enemy.currentCount = Math.max(0, enemy.currentCount - unitsLost);
          }
        }
      }
    }
    
    return newState;
  }

  /**
   * Type guard to check if a unit is a player group
   */
  private isPlayerGroup(unit: any): unit is BattlePlayerGroup {
    return 'unit' in unit && 'totalHealth' in unit && !('type' in unit);
  }

  /**
   * Legacy: Apply attack result to state and return new state (pure function approach)
   */
  public applyAttackResult(state: UnifiedBattleState, result: { combatAction: CombatAction; damageDealt: number; targetEliminated: boolean }): UnifiedBattleState {
    const newState = this.deepCloneState(state);
    
    // Add to combat log
    newState.combatLog.push({
      ...result.combatAction,
      turn: state.currentTurn
    });
    
    // Add damage to total
    newState.totalDamageDealt += result.damageDealt;
    
    // Update unit states based on attack result
    if (result.targetEliminated) {
      // Handle eliminated targets
      if (result.combatAction.attacker.includes('x ')) {
        // Player attacked enemy - find and eliminate mortal enemy
        const enemyIndex = newState.enemies.findIndex(e => 
          e.type === 'mortal' && this.getEnemyDisplayName(e) === result.combatAction.target);
        if (enemyIndex >= 0 && newState.enemies[enemyIndex].type === 'mortal') {
          (newState.enemies[enemyIndex] as any).currentCount = 0;
        }
      } else {
        // Enemy attacked player - eliminate player group
        const targetGroup = newState.playerGroups.find(g => g.unit.name === result.combatAction.target);
        if (targetGroup) {
          targetGroup.totalHealth = 0;
          targetGroup.count = 0;
        }
      }
    } else {
      // Apply damage to target (non-eliminated)
      const targetGroup = newState.playerGroups.find(g => g.unit.name === result.combatAction.target);
      if (targetGroup && result.damageDealt > 0) {
        targetGroup.totalHealth = Math.max(0, targetGroup.totalHealth - result.damageDealt);
        // Calculate units lost based on damage
        const unitHealth = targetGroup.unit.health;
        const unitsLost = Math.floor(result.damageDealt / unitHealth);
        targetGroup.count = Math.max(0, targetGroup.count - unitsLost);
      }
    }
    
    return newState;
  }
  
  /**
   * Get display name for enemy (helper)
   */
  private getEnemyDisplayName(enemy: Enemy): string {
    switch (enemy.type) {
      case 'generic':
        return enemy.name;
      case 'infinite':
        return enemy.unit.name;
      case 'mortal':
        return enemy.unit.name;
      default:
        return 'Unknown Enemy';
    }
  }

  /**
   * Legacy: Add combat action to the battle log
   */
  public addCombatAction(action: CombatAction): void {
    this.state.combatLog.push({
      ...action,
      turn: this.state.currentTurn
    });
  }

  /**
   * Legacy: Add damage dealt to total
   */
  public addDamageDealt(damage: number): void {
    this.state.totalDamageDealt += damage;
  }

  /**
   * Check if battle should end based on current state and return new state with battleEnded flag updated
   * Pure function approach - returns new state instead of mutating
   */
  public checkBattleEndPure(state: UnifiedBattleState, maxTurns: number = UNIFIED_BATTLE_CONSTANTS.MAX_BATTLE_TURNS): UnifiedBattleState {
    const alivePlayers = state.playerGroups.filter(group => group.totalHealth > 0);
    const attackableEnemies = state.enemies.filter(enemy => {
      switch (enemy.type) {
        case 'generic':
          return true; // Generic enemies always attack
        case 'infinite':
          return true; // Infinite enemies always attack  
        case 'mortal':
          return enemy.currentCount > 0; // Mortal enemies attack if they have units left
        default:
          return false;
      }
    });
    
    // Battle ends if either side cannot continue
    const playersEliminated = alivePlayers.length === 0;
    
    // Enemies are eliminated only if:
    // 1. No enemies can currently attack, AND 
    // 2. All enemies in the battle are mortal type (generic/infinite enemies can never be eliminated)
    const hasOnlyMortalEnemies = state.enemies.every(e => e.type === 'mortal');
    const enemiesEliminated = hasOnlyMortalEnemies && attackableEnemies.length === 0;
    
    // Battle also ends if it exceeds maximum turns (stalemate protection)
    const maxTurnsReached = state.currentTurn >= maxTurns;
    
    const shouldEnd = playersEliminated || enemiesEliminated || maxTurnsReached;
    
    return {
      ...state,
      battleEnded: shouldEnd
    };
  }

  /**
   * Legacy method for backward compatibility - delegates to pure function
   */
  public checkBattleEnd(): void {
    const currentState = this.getBattleState();
    const newState = this.checkBattleEndPure(currentState);
    this.applyStateUpdate(newState);
  }

  /**
   * Check if an enemy can attack (helper for battle end detection)
   */
  private canEnemyAttack(enemy: Enemy): boolean {
    switch (enemy.type) {
      case 'generic':
        return true; // Generic enemies always attack
      case 'infinite':
        return true; // Infinite enemies always attack  
      case 'mortal':
        return enemy.currentCount > 0; // Mortal enemies attack if they have units left
      default:
        return false;
    }
  }

  /**
   * Get battle outcome based on current state
   */
  public getBattleOutcome(maxTurns: number = UNIFIED_BATTLE_CONSTANTS.MAX_BATTLE_TURNS): 'player_victory' | 'player_eliminated' | 'stalemate' {
    const alivePlayers = this.getAlivePlayerGroups();
    const attackableEnemies = this.getAttackableEnemies();
    
    if (alivePlayers.length === 0) {
      return 'player_eliminated';
    }
    
    if (attackableEnemies.length === 0 && this.state.enemies.every(e => e.type === 'mortal')) {
      return 'player_victory';
    }
    
    if (this.state.currentTurn >= maxTurns) {
      return 'stalemate';
    }
    
    // Should not reach here during active battle
    return 'stalemate';
  }

  /**
   * Check if battle has ended
   */
  public isBattleEnded(): boolean {
    return this.state.battleEnded;
  }

  /**
   * Get current turn number
   */
  public getCurrentTurn(): number {
    return this.state.currentTurn;
  }

  /**
   * Get current action number within the turn
   */
  public getCurrentAction(): number {
    return this.state.currentAction;
  }

  /**
   * Get total damage dealt so far
   */
  public getTotalDamageDealt(): number {
    return this.state.totalDamageDealt;
  }

  /**
   * Get complete combat log
   */
  public getCombatLog(): CombatAction[] {
    return [...this.state.combatLog];
  }

  /**
   * Force end battle (for external termination)
   */
  public endBattle(): void {
    this.state.battleEnded = true;
  }

  /**
   * Reset battle to initial state (for restarting)
   */
  public resetBattle(config: UnifiedBattleConfiguration): void {
    this.state = this.initializeBattleState(config);
  }
}